.lk {

    @media(max-width: 991px) {
        margin-bottom: 30px;
    }

    &-container {
        @extend .container;
        display: flex;
        position: static;
    }

    &-nav {
        max-width: 400px;
        width: 50%;
        padding: 50px 0 80px;
        background: $lightgrey;
        position: relative;
        @media(max-width: 991px) {
            background: none;
            max-width: 100%;
            width: 100%;
            padding: 0;
        }

        &::after {
            content: '';
            position: absolute;

            top: 0;
            right: 100%;
            display: flex;
            height: 100%;
            width: 100vw;
            background: $lightgrey;
            pointer-events: none;
            transition: all 0.4s ease-in-out;
            z-index: -1;

            @media(max-width: 991px) {
                display: none;
            }

        }

        &__title {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            margin-bottom: 30px;

            @media(max-width: 991px) {
                display: none;
            }

            & span {
                font-size: 18px;
                line-height: 22px;
                position: relative;

                &:after {
                    content: '';
                    width: calc(100% + 40px);
                    position: absolute;
                    left: 0;
                    bottom: -5px;
                    background: $black;
                    display: flex;
                    display: flex;
                    height: 1px;

                }
            }
            &.welcome {
                & a {
                    display: none;
                }
                @media(max-width:991px) {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    padding: 40px 0;
                    position: relative;

                    &:before {
                        content: '';
                        height: 100%;
                        width: calc(100% + 30px);
                        display: flex;
                        background: $lightgrey;
                        position: absolute;
                        left: -15px;
                        top: 0;
                    }

                    & span {
                        font-size: 16px;
                        font-weight: 400;
                        line-height: 17px;
                        &:after {
                            content: none;
                        }
                    }

                    & a {
                        display: flex;
                        color: $red;
                        font-size: 10px;
                        font-weight: 500;
                        line-height: 12px;
                        position: relative;
                        text-transform: uppercase;

                        &:before {
                            content: '';
                            position: absolute;
                            left: 0;
                            bottom: 0;
                            height: 1px;
                            width: 100%;
                            background: $red;
                        }

                    }
                }
            }
        }

        & ul {
            list-style-type: none;
            margin-bottom: 50px;

            @media(max-width:991px) {
                margin-bottom: 0;
            }

            & li {
                display: flex;
                align-items: flex-end;
                justify-content: flex-start;
                padding: 12px 0;
                font-size: 16px;

                @media(max-width: 574px) {
                    padding: 10px 0;
                    font-size: 14px;
                }

                &.active {
                    font-weight: 500;
                }

                & a {
                    font-size: inherit;
                    line-height: inherit;
                }

                & svg {
                    width: 18px;
                    height: 18px;
                    fill: $black;
                    margin-right: 30px;

                    @media(max-width: 574px) {
                        margin-right: 25px;
                    }
                }
            }
        }
        &_logout {
            text-transform: uppercase;
            color: $red !important;
            padding: 7px 0;
            position: relative;
            font-size: 12px;
            &:before {
                content: '';
                position: absolute;
                left: 0;
                bottom: 0;
                height: 1px;
                width: 100%;
                background: $red;
            }

            @media(max-width:991px) {
                display: none;
            }

        }
    }

    &-content {
        display: flex;
        width: 100%;
        position: relative;

        @media(max-width:991px) {
            //transform: translateX(100%);
            position: fixed;
            top: 0;
            left: 0;
            padding-left: 150px;
            min-height: 101vh;
            z-index: 20;
            width: 100%;
            transition: background-color 0.3s ease-in-out;
            visibility: hidden;

            &.active {
                // transform: translate(0, 0); 
                height: 101vh;
                box-shadow: 0 2px 16px 0 rgb(5 5 5 / 20%);
                -webkit-box-shadow: 0 2px 16px 0 rgb(5 5 5 / 20%);
                background: rgba(0,0,0,.2);
                transition: background-color 0.3s ease-in-out;
                transition-delay: 0.3s;
            }
        }

        @media (min-width: 575px) and (max-width:991px) {

            &.active {
                // width: calc(100% - 150px);
            }

        }
        @media (max-width: 574px) {
            &.active {
                //width: calc(100% - 50px);
                padding-left: 0px;
            }

        }

        &-modal {
            flex-direction: column;
            width: 100%;
            background-color: #fff;
            display: none;

            @media(max-width:991px) {
                display: none;
                position: absolute;
                height: 100%;
                transition: all 0.4s ease-in-out;
                transform: translate(100%, 0);
                width: calc(100% - 150px);
                &.show {
                    transition: all 0.4s ease-in-out;
                    transform: translate(0, 0);
                }
            }
            @media(max-width: 574px) {
                //width: calc(100% - 50px);
                width: 100%;
            }

            &__header {
                @extend .modal-header;
                justify-content: flex-start;
                display: none;

                & .title {
                    font-weight: 500;
                    font-size: 16px;
                }

                @media(max-width:991px) {
                    display: flex;
                }
            }
            &__body {
                display: flex;
                width: 100%;
                padding: 50px 0;

                @media(max-width:991px) {
                    overflow-y: scroll;
                    display: initial;
                    flex-direction: column;
                    padding: 0;
                    & > [class*="col-"] {
                        flex: 0 0 100%;
                        max-width: 100%;
                        margin-top: 30px;
                        margin-bottom: 70px;
                    }
                }

                @media(max-width: 574px) {
                    & > [class*="col-"] {
                        margin-bottom: 90px;
                        margin-top: 20px;
                    }
                }
                & button.modal-info__icon {
                    width: auto;
                    height: auto;
                    color: $red;
                    margin-bottom: 18px;
                    margin-left: 0;
                    margin-top: 50px;
                    padding: 0;
                }

                & button.outline {
                    margin-top: 80px;
                    padding: 10px 35px;
                    height: 40px;

                    @media(max-width:574px) {
                        margin-top: 40px;
                        width: 100%;
                        max-width: 100%;
                    }
                }

                & .collapse {
                    &-question {
                        text-transform: initial;
                        font-size: 14px;
                       // border-bottom: 1px solid $black;
                        display: inline-block;
                        width: auto;
                        padding: 5px 0;
                        cursor: pointer;

                        &.active {
                            border-bottom: none;
                        }
                    }

                    &-content {
                        &-question {
                            @extend .collapse-content;
                            margin-bottom: 10px;
                            max-height: 0;

                            & p {
                                font-weight: 300;
                            }
                        }
                        &-new-order {
                            @extend .collapse-content;
                            max-height: 0;
                        }
                    }
                }

                & .order {
                    border: 1px solid $lightgrey;
                    border-radius: 10px;
                    margin-bottom: 40px;
                }

            }
        }
    }
}

.history-list-order,
.history-list-withdrawal {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 0 25px 25px;
    position: relative;

    &::before {
        content: '';
        background: #89AC76;
        width: 5px;
        height: calc(100% - 25px);
        position: absolute;
        left: 0;
        top: 0;
    }
    &__item {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        width: 100%;
        padding: 16px 0;
        color: #787878;

        & > * {
            color: inherit;
        }

        & .data {
            margin-right: 40px;

            & .time {
                margin-left: 10px;
            }
        }
    }
}

.history-list-withdrawal {
    display: flex;
    flex-direction: column;
    width: 100%;
    &:before {
        background: #B0B7C6;
    }

    &__item {

        & b {
            font-weight: 500;
        }

        & .status {
            margin-left: 30px;
            font-weight: 500;
            width: 50%;

            &-success {
                color: #317F43;
            }
            &-pending {
                color: #FF9900;
            }
        }
    }
}

.order {
    &__item {
        padding: 25px;

        @media(max-width: 574px) {
            padding: 25px 15px;
        }

        & .collapse {
            position: relative;
            padding: 0 25px;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;

            @media(max-width: 574px){
                padding: 5px 15px;
            }

            &.status {
                &-new {
                    &::before {
                        background: #89AC76;
                    }
                }
                &-canceled {
                    &::before {
                        background: #3E5F8A;
                    }
                }
                &-processing {
                    &::before {
                        background: #F34723;
                    }
                }
                &-forming {
                    &::before {
                        background: #FDBCB4;
                    }
                }
                &-send {
                    &::before {
                        background: #01796F;
                    }
                }
                &-not-available {
                    &::before {
                        background: #FFD700;
                    }
                }

            }

            &::before {
                content: '';
                display: flex;
                width: 5px;
                height: 100%;
                position: absolute;
                left: 0;
                top: 0;
            }

            &.active {
                & .arrow {
                    transition: all 0.3s ease-in-out;
                    &::before {
                        transform: rotate(45deg);
                    }

                    &:after {
                        transform: rotate(-45deg);
                    }
                }
                & .order__cost {
                    display: none;
                }
            }

            .short-info {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                justify-content: center;

                & > * {
                    padding: 3px 0;
                }

                & small {
                    color: #787878;
                }
                & span {
                    text-transform: initial;
                    font-size: 16px;
                    font-weight: 500;
                }
            }

            & .arrow {
                width: 14px;
                height: 14px;
                display: flex;
                position: relative;

                &::after,
                &::before {
                    content: '';
                    width: 1px;
                    height: 12px;
                    position: absolute;
                    background: $black;
                    top: 0;
                    transition: all 0.3s ease-in-out;
                }

                &::before {
                    left: 2px;
                    transform: rotate(-45deg);
                }

                &:after {
                    right: 2px;
                    transform: rotate(45deg);
                }
            }
        }
        & .info-order {
            @media(max-width: 574px) {
                margin-bottom: 50px;
            }
            & p {
                color: #787878;
            }

            & p,
            li {
                margin-bottom: 18px;
            }
        }
    }
    &__control {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;

        @media(max-width: 450px) {
            flex-direction: column;
        }

        & button.outline,
        button.primary {
            margin: 0;
            height: 40px;
            width: 100%;
            max-width: 175px;

            @media(max-width: 450px) {
                width: 100%;
                max-width: 100%;
                margin-bottom: 10px;
            }
        }

        & button.outline {
            @media(max-width: 450px) {

                margin-bottom: 0;
            }
        }
    }

    &__cost {
        & span {
            font-size: 18px;
            font-weight: 500;
            text-transform: initial;
            &:last-child {
                font-size: 14px;
            }
        }
        @media(max-width: 574px) {
            display: none;
        }
    }
    &-total {
        &-list {
            display: flex;
            flex-direction: column;
            width: 100%;
            margin-bottom: 30px;
            margin-top: 30px;

            @media(max-width: 574px) {
                margin-bottom: 25px;
                margin-top: 20px;
                }
        }
        &__item {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 100%;
            margin-bottom: 15px;

            &-title {
                color: #787878;

            }
        }

        &__value {

            &.cost {
                font-size: 18px;
                font-weight: 500;

                & span {
                    margin-left: 4px;
                    font-size: 14px;
                    font-weight: 500;
                }
            }
        }
    }

    & .product {
        &-item {
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 100%;
            padding: 10px 0 15px;

            @media(max-width: 574px) {
                flex-direction: column;

                &__info {
                    order: 3;
                }
            }

            &__info {
                display: flex;
                min-width: 60%;
                width: 100%;
                margin-top: 20px;
                align-items: center;
                justify-content: flex-start;

                @media(max-width: 574px) {
                    margin-top: 5px;
                }

            }
            &__img {
                max-width: 100px;
                max-height: 100px;
                display: flex;
                justify-content: center;
                align-items: center;
                overflow: hidden;

                & img {
                    width: 100%;
                }

            }

            &__name {
                margin-top: 20px;
                max-width: 100%;
                margin-right: 10px;

                & > * {
                    text-overflow: ellipsis;
                    -webkit-line-clamp: 1;
                    display: -webkit-box;
                    -webkit-box-orient: vertical;
                    min-height: 17px;
                    overflow: hidden;

                }

                @media(max-width: 574px) {
                    margin-top: 0;
                }
            }

            &__count,
            &__price {
                padding-top: 40px;
                width: 100%;
                display: flex;
                justify-content: center;
                align-items: center;

                @media(max-width: 574px) {
                    justify-content: center;
                    padding-top: 0;
                    font-size: 14px;
                }
            }
            &__price {
                justify-content: flex-end;
                align-items: flex-end;
                font-size: 18px;

                & span {
                    font-size: 14px;
                    margin-left: 4px;
                }

                @media(max-width: 574px) {
                    justify-content: center;
                    font-size: 14px;

                    & span {
                        font-size: inherit;
                    }
                }

            }
            &__order {
                display: flex;
                width: 100%;

            }

            & .coll {
                display: flex;
                justify-content: center;
                position: relative;
                width: 100%;
                @media(max-width: 574px) {
                    flex-direction: column;
                }

                & .title {
                    position: absolute;
                    top: -10px;
                    color: #787878;
                    font-size: 12px;

                    @media(max-width: 574px) {
                        position: initial;
                        top: none;
                        display: flex;
                        justify-content: center;
                        margin-bottom: 10px;
                        font-size: 10px;
                    }
                }

                &:last-child {

                    justify-content: flex-end;

                    @media(max-width: 574px) {

                        justify-content: center;
                    }

                }
            }
        }

    }
}

.bonus {
    &-list {
        &__title {
            font-weight: 500;
            margin-bottom: 20px;
        }
    }
    &-wrap {
        display: flex;
        margin-bottom: 20px;
        padding: 20px;
        border: 1px solid $lightgrey;

        border-radius: 10px;
    }
    &-info-list {
        list-style: none;
        margin-bottom: 120px;

        & li {
            display: flex;
            padding: 10px 0;

            & .modal-info__icon {
                margin: 0;
                color: #787878;
                border: 2px solid #787878;
                padding-bottom: 2px;
                padding-left: 10px;
                padding-right: 10px;
                margin-left: 40px;
            }

            & span {
                color: $red;
                margin-left: 14px;
                font-weight: 500;
            }
        }
    }
    &-item {
        display: flex;
        flex-direction: column;
        position: relative;
        width: 100%;
        min-height: 30px;

        &__header {
            display: flex;
            width: 100%;
            justify-content: flex-end;
            align-items: center;

            & > * {
                width: 33%;
                display: flex;
                justify-content: center;
                &:last-child {
                    justify-content: flex-end;
                }
                &:first-child {
                    justify-content: center;
                }
            }
        }
        &__body {
            display: flex;
            align-items: center;
            justify-content: flex-start;
            height: 100%;
            width: 100%;
            padding-bottom: 15px;
            & > * {
                width: 33%;
                display: flex;
                &:last-child {
                    justify-content: flex-end;
                }
                &:nth-child(2) {
                    justify-content: center;
                }

            }
        }

    }
    &-status {
        &::before {
            content: '';
            display: flex;
            width: 5px;
            height: 100%;
            position: absolute;
            left: 0;
            top: 0;
        }

        &-new {
            &::before {
                background: #89AC76;
            }
        }
        &-canceled {
            &::before {
                background: #3E5F8A;
            }
        }
        &-processing {
            &::before {
                background: #F34723;
            }
        }
        &-forming {
            &::before {
                background: #FDBCB4;
            }
        }
        &-send {
            &::before {
                background: #01796F;
            }
        }
        &-not-available {
            &::before {
                background: #FFD700;
            }
        }

    }
    &-short-info {
        display: flex;
        flex-direction: column;
        margin-left: 25px;
    }

    &__balance,
    &__operation {
        color: $red;
    }

}

#user-delivery {

    & form {
        max-width: 450px;

        @media(max-width: 574px){
            max-width: 100%;
        }
    }
    & .deliv {

        &-type {
            margin-top: 50px;
            margin-bottom: 30px;

            & label {
                font-size: 16px;
                line-height: 20px;
            }

            @media(max-width: 574px){
                margin-bottom: 15px;
            }
        }
        &-settings {
            margin-bottom: 40px;

            @media(max-width: 574px){
                margin-bottom: 20px;
            }
        }
        &-about {
            & li {
                font-size: 12px;
                font-style: normal;
                font-weight: 300;
                line-height: 15px;
            }

            & p {
                margin-bottom: 15px;
            }
            & ul {
                padding-left: 15px;
            }

        }
    }
}
