button {
    padding: 2px;
    background: none;
    border: none;

    background-repeat: no-repeat;
    background-position: center center;
    background-size:20px;
    outline: none;
    cursor: pointer;
   

    & svg {
        fill: $black;
        width: 20px;
        height: 20px;
    }

    &.more-filters {
        border: 1px solid $lightgrey;
        background: $white;
        color: $black;
        width: 100%;
        padding: 12px 40px 12px 16px;
        display: flex;
        justify-self: flex-start;
        align-items:center;
        position: relative;
        font-weight: 500;
        text-transform: uppercase;
        min-height: 45px;
        margin-bottom: 6px;
        &::before {
            content: "";
            width: 1px;
            height: 100%;
            position: absolute;
            top: 0;
            right: 40px;
            background: #f3f3f3;
        }

        &::after {
            position: absolute;
            content: "";
            top: 58%;
            transform: translateY(-50%);
            right: 13px;
            width: 0;
            height: 0;
            border: 6px solid transparent;
            border-top-color: #000;
            transition: all .3s ease-in-out;
          }
    }

    &.promo{
        background: #E5E5E5;
        text-transform: uppercase;
        color:red;
        margin-bottom: 17px;
        &.hide{
            display: none;
        }
    }

    &.primary {
        background-color: $black;
        border: none;
        text-transform: uppercase;
        color: $white;
        font-weight: 500;
        padding: 10px 45px;
        display: flex;
        height: 45px;
        justify-content: center;
        transition: all .3s ease-in-out;
        align-items: center;

        &:disabled{
            pointer-events: none;
            background-color: $lightgrey;
            border: 1px solid $lightgrey;
        }
    }
    &.outline{
        background-color: transparent;
        border: 1px solid $black;
        text-transform: uppercase;
        color: $black;
        font-weight: 500;
        padding: 10px 45px;
        display: flex;
        height: 45px;
        justify-content: center;
        transition: all .3s ease-in-out;
        align-items: center;

        &:disabled{
            pointer-events: none;
            border: 1px solid $lightgrey;
        }
    }

    &.favorite{
        display: flex;
        width: 46px;
        justify-content: center;
        align-items: center;
        position: relative;

        &:hover{
            &:after{
                opacity: 1;
                visibility: visible;
            }
            &:before{
                opacity: 1;
                visibility: visible;
            }
        }
        &:after{
            content: 'Добавить в избранное';
            opacity: 0;
            bottom: 125%;
            right: 0;
            font-size: 12px;
            padding: 12px;
            position: absolute;
            border:1px solid $black;
            display: flex;
            flex-wrap: nowrap;
            width:200px;
            justify-content: center;
            z-index: 1;
            background: $white;
            visibility: hidden;
            transition: all .4s ease-in-out;
        }

        &:before{
            content:'';
            opacity: 0;
            z-index: 2;
            position: absolute;
            width: 10px;
            height: 10px;
            bottom: calc(100% + 7px);
            left: 50%;
            transform: translateX(-50%) rotate(45deg);
            border-width: 1px;
            border-style: solid;
            background: $white;
            border-color:  transparent $black $black transparent;
            visibility: hidden;
            transition: all .4s ease-in-out;
            
        }

        & path{
            &:last-child{
                fill: none;
            }
        }

        &.active{
            & path{
                &:last-child{
                    fill: $black;
                }
            }

            &:after{
                content: 'Удалить с избранного';
            }
            

            &:hover{
                &:after{
                    opacity: 1;
                    visibility: visible;
                }
                &::before{
                    opacity: 1;
                    visibility: visible;
                }
            }
        }
    }

    &.modal-info__icon{
        // width: 20px;
        width: auto;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        height: 20px;
        position: relative;
        padding: 0;
        background-size: 100%;
        @media(max-width: 575px){
            padding-left: 26px;
            padding-right: 15px;
        }

        & p{
            text-transform: uppercase;
            font-size: 11px;
            font-weight: 500;
            color: #afafaf;
            text-align: left;
            pointer-events: none;
            @media(max-width: 575px){
                order: 2
            }
        }

        & svg{
            width: 20px;
            height: 20px;
            display: flex;
            fill: #afafaf;
            transition: all .3s ease-in-out;
            pointer-events: none;
            margin-top: -1px;
            margin-left: 8px;
            

            @media(max-width: 575px){
                position: absolute;
                left: 0;
                top: 50%;
                transform: translateY(-50%);
                order: 1;
                margin-left: 0;
                margin-right: 6px;
            }
        }

        &:hover{
            & svg{
                fill: $black;
                transition: all .3s ease-in-out;
            }
        }
    }

}
