.order {
    &-block {

        &-head {
            @extend .basket-block-head;
            padding: 106px 0;
            &-wrap {
                @extend .basket-block-head-wrap;
            }
        }

        &-body{
            & [class*="col-"] {
                @media(max-width: 767px){
                    &:first-child{
                        margin-bottom: 120px;
                    }
                }
                
            }
        }

        &-footer {
            @extend .basket-block-footer;
            align-items: flex-start;

            @media(max-width: 850px){
                & .col-md-6{
                    flex: 0 0 100%;
                    max-width: 100%;
                }
            }
        }
    }

    &-form {
        &-auth,
        &-callback {
            width: calc(100% - 10px);
           // max-width: 600px;
            padding-top: 40px;
            line-height: 40px;
            display: flex;
            flex-direction: column;

            @media(max-width: 767px) {
                width: 100%;

            }

            & h2{
                font-size: 18px;
            }

            & .primary {
                width: 100%;
                margin-top: 50px;
                font-weight: 500;
                margin-bottom: 20px;

                @media(max-width: 574px){
                    margin-bottom: 10px;
                }
            }

            & a{
                margin-bottom: 7px;

                &:last-child{
                    margin-bottom: 0;
                }
            }

            & .form-group{
                

                &-row{
                    margin-bottom: 0;
                }
                
                &:last-child{
                    margin-bottom: 0;
                }
            }
            
        }
    }

    &-product {
        &__list {
            display: flex;
            justify-content: flex-start;
            align-items: flex-start;
            flex-wrap: wrap;
            list-style: none;
            width: 100%;
            padding-bottom: 40px;
        }

        &__item {
            width: 100%;
            max-width: 33.33%;
            position: relative;

            @media(max-width: 991px) {
                max-width: 50%;
            }

            @media(max-width: 767px) {
                max-width: 100%;
            }

            & [type="checkbox"]{
                visibility: hidden;
                opacity: 0;
                position: absolute;
                right: 0;
                top: 50%;
                transform: translateY(-60%);
                transition: all .3s ease-in-out;

                &.visible{
                    visibility: visible;
                    opacity: 1;
                    transition: all .3s ease-in-out;
                }
            }
        }

        &__link{
            display: flex;
            align-items: center;
        }

        &__img{
            &-wrapper{

                @media(max-width: 574px){
                    height: 80px;
                    width: 80px;
                }
            }
        }
        &-quantity{
            font-size: 12px;
            font-weight: 500;
            margin-top: 2px;

            &:before{
                content:"x";
            }
        }

        &__info{
            & p:first-child{
                @media(max-width: 574px){
                    display: none;
                }
            }
        }
    }

    &-tab {
        &-delivery,
        &-payment {
            &-nav {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                justify-content: flex-start;
                width: 100%;
               // max-width: 600px;

                @media(max-width:767px) {
                    width: 100%;
                    min-width: 100%;

                }

                & p{
                    margin-bottom: 10px;
                    font-size: 16px;
                }

                &__item {
                    display: flex;
                    justify-content: flex-start;
                    width: 100%;
                    font-size: 16px;
                    font-weight: 500;
                    padding: 45px 20px;
                    border: 2px solid $lightgrey;
                    border-radius: 10px;
                    margin-bottom: 10px;
                    position: relative;
                    outline: none;
                    transition: border .3s ease-in-out;

                    @media(max-width: 574px){
                        padding: 40px 20px;
                    }
                    
                    &.active, &:hover {
                        border: 2px solid $black;
                        color: $black;
                    }
                }
            }

            &__content {
                display: none;
                opacity: 0;
                transition:all .3s ease-in-out;
                margin-top: 20px;
                padding-left: 10px;

                @media(max-width: 767px) {
                    max-width: 100%;
                    padding-left: 0;
                    padding: 10px 10px;
                }

                &-info{
                    margin-top: 35px;

                    & p{
                        margin-bottom: 5px;
                    }
                    
                }

                &.active {
                    opacity: 1;
                    transition:opacity .3s ease-in-out;
                    width: 100%;
                    display: block;
                }
                

                & .select-selected {
                    min-height: 50px;
                    padding: 10px 15px;
                    font-size: 16px;
                    border-radius: 4px;
                }
                & textarea {
                    min-height: 50px;
                    padding: 10px 15px;
                    font-size: 16px;
                    border-radius: 4px;
                    border: 1px solid $lightgrey;
                    margin-bottom: 10px;
                    width: 100%;
                }

                & .form-group {
                    & input {
                        margin-bottom: 10px;
                    }
                }

                & .select {

                    margin-bottom: 10px;
                }

                @media(max-width:574px) {
                    margin-top: 0;
                }
            }

        }
    }

    &-delivery,
    &-payment,
    &-user,
    &-send-form{
        display: flex;
        flex-wrap: wrap;
        padding-bottom: 150px;
        width: 100%;

        @media(max-width: 574px){
            padding-bottom: 100px;
        }
        &-wrap {
            display: flex;
            width: 100%;
        }

        &__title{
            padding: 10px 0;
        }

        & .order-form-callback{
            width: 100%;
            max-width:100%;

        }
    }

    &-send-form{
        position: relative;
        &:before{
            content: '';
            width: calc(100% + 100px);
            height: 1px;
            background: $lightgrey;
            position: absolute;
            top: -50px;
            left: -50px;

            @media (max-width: 1400px) {
                width: 100%;  
                left: 0;            
            }

            @media (max-width: 1100px) {
                top: -28px;             
            }

        }
    }

    &-user {
        padding-bottom: 150px;
        @media (max-width: 574px) {
            padding-bottom: 100px;
        }
    }
}

#send-order{
    width: 100%;

    @media(max-width: 767px){
        max-width:100%;
    }
}

#checkout .order-block-footer{
    &:before{
        content: none;
    }
}