.seo{
    position: relative;

    &-separator{
        &:before{
            content: '';
            width: 100%;
            height: 1px;
            background:#E5E5E5;
            margin-bottom: 16px;
        }
    }

    & * {
        font-size: 12px;
    }

    



    ul,ol{
        margin-bottom: 20px;
        padding-left: 20px;

        li{
            margin-bottom: 20px;

            &:last-child{
                margin-bottom: 0;
            }
        }
    }

    & p{

        margin-bottom: 20px;
    }

    & *:last-child{
        margin-bottom: 0;
    }
}