.main-slider {
    position: relative;


    @extend .swiper-container;

    margin-top: -90px;


    @media(max-width: 992px){
        margin-top: 0;
    }

    & .primary {
        background-color: rgba(0,0,0,.4);
        text-transform: uppercase;
        color: #fff;
        font-weight: 300;
        padding: 10px 45px;
        display: flex;
        justify-content: center;
        transition: all 0.3s ease-in-out;
        margin-bottom: 0;
        margin-right: 13px;
    }

    &__wrapper {
        @extend .swiper-wrapper;
    }

    &__item {
        @extend .swiper-slide;
        position: relative;
        margin: 0 auto;
        
        & img {
            width: 100%;
        }

    }

    &__title {
        display: flex;
        font-weight: 500;
        font-size: 32px;
        line-height: 44px;
        color: $black;
        font-style: normal;
        margin-bottom: 70px;

        @media(max-width: 991px) {
            font-size: 30px;
            line-height: 37px;
            margin-bottom: 60px;
        }

        @media(max-width: 767px){
            font-size: 24px;
            margin-bottom: 60px;
        }

        @media(max-width: 574px) {
            font-size: 20px;
            line-height: 24px;
        }

    }

    &__content {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        margin: 0 auto;
        position: absolute;
        padding: 0 15px;
        height: 100%;
        width: 100%;
        max-width: 1300px;
        left: 50%;
        transform: translateX(-50%);
    }

    &__text {

        position: absolute;
        left: 5%;
        bottom: 28%;
        color: $black;
        font-size: 20px;
        transition: all .9s ease-in-out;
        

        span {
            display: flex;
            color: inherit;
            margin-bottom: 30px;
            font-size: inherit;
            font-size: 18px;
            font-style: normal;
            font-weight: 400;
            line-height: 24px;
            margin-bottom: 22px;


            @media(max-width: 991px) {
                font-size: 16px;
                margin-bottom: 15px;
            }

            @media(max-width: 767px){
                margin-bottom: 20px;
            }

            @media(max-width: 574px) {
                font-size: 14px;
                margin-bottom: 15px;
            }
        }

    }

    &-pagination {
        width: 100%;
        display: flex;
        width: 100%;
        justify-content: center;
        align-items: center;

        &-wrap {
            display: flex;
            width: 100%;
            justify-content: center;
            align-items: center;
            position: absolute;
            bottom: 30px;
            left: 0;
            z-index: 1;
        }

        &__item {
            background: $white;
            font-size: 0;
            opacity: 0.5;
            height: 2px;
            width: 40px;
            transition: all 0.3s ease-in-out;
            margin-right: 10px;
            display: flex;

            &.active {
                opacity: 1;
            }
        }
    }
}
