.products {
    &-title {
        display: flex;
        justify-content: flex-start;
        height: 50px;        
        & h2 {
            display: flex;
            justify-content: center;
            width: 100%;
            font-weight: 400;
        }
    }

    &-slider {
        @extend .container;
        @extend .swiper-container;
        padding: 80px 15px;

        @media(min-width: 575px) and (max-width: 991px){
            padding: 50px 15px; 
        }
        @media (max-width: 574px){
            padding: 40px 15px; 
        }

        &__wrapper{
            @extend .swiper-wrapper;
        }


        &__item {
            @extend .swiper-slide;
            display: flex !important;
            justify-content: center;
            align-items: center;
            flex-direction: column;

            & a{
                display: flex;
                flex-direction: column;
                align-items: center;
            }

            & span{
                margin-top: 30px;
            }

            & img{
                display: flex;
                max-width: 130px;
                width: 100%;

                @media(max-width: 991px){
                    max-width: 80px;
                }

                @media(min-width: 1300px){
                    max-width: 100px;
                }
                }

        }

        &__img{
            width: 100%;
            overflow: hidden;
        }

        &-button {

            &-prev{
            transform: scale(-1, -1);
            width: 100%;
            height: 100%;
            background: url('./../img/next.svg') center center no-repeat;
            background-size: 60%;
            }  
            
            &-next{
                width: 100%;
                height: 100%;
                background: url('./../img/next.svg') center center no-repeat;
                background-size: 60%;
                } 

        &-wrap {
            display: flex;
            align-items: center;
            justify-content: center;
            height: 100%;
            width: 100px;
        }
    }
    }

}

    

