form{
    width: 100%;
    // padding-top: 25px;
    & label{
        margin: 10px 0;
    }

    & .primary{
        margin-top: 40px;
        width: 100%;
    }



    & h2{
        margin-bottom: 20px;
    }
}

.form{
    &-group{
        width: 100%;
        margin-bottom: 10px;
        display: flex;
        flex-direction: column;

        &.invalid {
            color:red;

            & label{
                color:inherit
            }
            & input{
                color:inherit;
                border-color:$red;
                background-color:rgba($red, .07);
            }
          }

        &-row{
            display: flex;
            flex-direction: row;
            margin-bottom: 10px;
            & input{
                margin-right: 10px;

                &:last-child{
                    margin-right: 0;
                }
            }

        & .form-group{
            margin-right: 10px;

            &:last-child{
                margin-right: 0;
            }
        
        }
        }
    }
}