.advantages{
    background: $grey;
    min-height: 450px;
    @media(min-width: 767px) and (max-width: 991px){
        &.ptb{
            padding-bottom: 30px;
        }
    }

    @media(min-width: 991px){
        padding-top: 100px;
    }

    &-article{
        
        @media(max-width: 992px){
            margin-bottom: 50px;
        }

        &__head{
            display: flex;
            align-items: center;
            margin-bottom: 60px;

            @media(max-width: 992px){
                margin-bottom: 20px;
            }
        }

        & img{
            display: flex;
            max-width:  70px;
            max-height: 70px;
            margin-right: 20px;
            border-radius: 50%;
        }
    
        & h1,h2,h3,h4,h5,h6{
            font-size: 28px;
            
            font-style: normal;
            font-weight: 400;
            line-height: 23px;
            letter-spacing: 0.01em;

            @media (min-width: 768px) and (max-width: 992px){
                font-size: 24px;
            }

            @media (min-width: 575px) and (max-width: 767px) {
                font-size: 22px;
            }

            @media(max-width: 575px){
                font-size: 19px;
            }


        }
        & p{
            font-size: 16px;
            font-style: normal;
            font-weight: 300;
            line-height: 20px;
            letter-spacing: 0.01em;

            @media(min-width: 768px) and (max-width: 991px){
                line-height: 20px;
                line-height: 18px;
            }
            @media (max-width: 991px){
                font-size: 14px;
            }

        }
    }
}