.footer{
    min-height: 30vh;
    position: relative;

    &-nav{
        &-list{
            list-style: none;
            margin: 0;
            padding: 0 40px;

            @media(max-width: 767px){
                padding: 0 10px;        
            }
            @media(min-width: 768px) and(max-width: 1200px){
                padding: 0 20px;        
            }


            &.social{
                display: flex;
                justify-content: flex-start;
                align-items: center;

                & .footer-nav__item{
                    background: red;
                    margin-right: 5px;
                    border-radius: 50%;
                    margin-bottom: 0;
                    overflow: hidden;

                    &:last-child{
                        margin-right:0;
                    }
                }
            }

            &.info{
                & .footer-nav__item{
                    margin-right: 20px;
                    &:last-child{
                        margin-right:0;
                    }
                }
            }
        }

        &__item{
            margin-bottom: 24px;

            @media(max-width: 992px){
                margin-bottom: 16px;
            }
            
            & a{
                width: 100%;
                height: 100%;
                display: flex;
                font-size: 14px;
                font-style: normal;
                font-weight: 400;
                line-height: 18px;
                letter-spacing: 0.01em;
                

            }
        }

        &__link{
            transition: all .3s ease-in-out;
            color: $black;
        }

    }

    &-logo{
        max-width: 200px;
        margin-bottom: 40px;

        @media(min-width:768px)and(max-width:1200px){
            max-width: 170px;
        }

        @media(min-width:575px)and(max-width:991px){
            max-width: 150px;
        }
        @media(max-width:574px){
           display: none;
        }
    }
}