.select {
  position: relative;
  border: 1px solid $lightgrey;
  border-radius: 2px;

  &-filter{
    margin-bottom: 16px;
    height: 45px;

    &:first-child{
      margin-top: 16px;
    }

    & .select-selected{
      height: 100%;
    }
  }

  &.sort{
    border: none;
    display: inline-flex;
    margin-bottom: 6px;
    min-height: 45px;

    &:before, &::after{
      content:none;
    }

    & .select-selected{
      font-weight: 500;
      color:$black;
      padding: 8px 20px 8px 16px;
      padding-left: 0;
      &:after{
        position:initial;
        margin-top: 5px;
        margin-left: 12px;
        transform: initial;
        top: initial;
        right: initial;
      }

      &.select-arrow-active:after {
        border-color: transparent transparent $black transparent;
        margin-top: -5px;
      }
    }

    & .select{
      &-items {
            min-width: 170px;
      & div{
        padding: 10px 15px;
      color:$black;
      }
    }
    &-hide{
      display: block;
    }
    
  }
  }

  &:before{
    content:'';
    width: 1px;
    height: 100%;
    position: absolute;
    right: 40px;
    background: $lightgrey;
  }

  & select {
    display: none; 
  }
  
  
  &-selected {
    background-color: $white;
    color: #787878;
    padding: 8px 40px 8px 16px;
    width: 100%;
    display: flex;
    align-items: center;
    border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
    cursor: pointer;

    &:after {
      position: absolute;
      content: "";
      top: 58%;
      transform: translateY(-50%);
      right: 13px;
      width: 0;
      height: 0;
      border: 6px solid transparent;
      border-color: $black transparent transparent transparent;
      transition: all .3s ease-in-out;
    }

    &.select-arrow-active:after {
      border-color: transparent transparent $black transparent;
      top: 18px;
    }
  }

  &-items {
    position: absolute;
    background-color: $white;
    top: 100%;
    left: 0;
    right: 0;
    z-index: 99;
    border: 1px solid #e5e5e5;
    overflow: auto;
    max-height: 230px;
    box-shadow: 0 5px 7px 1px rgba(0,0,0,0.14);
    padding-top: 10px;
    padding-bottom: 20px;
    opacity: 1;
            transition: all .2s ease-in-out;
            visibility: visible;
    

  
  
    & div{
      color: #787878;
      height: 45px;
      display: flex;
      align-items:center;
    padding: 10px 30px;
    border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
    cursor: pointer;
  
    &:last-child{
      border: none;
    }
    }

    & div:hover, .same-as-selected {
      background-color: #f3f3f3;
    }
  }

  &-hide {
    opacity: 0;
    transition: all .2s ease-in-out;
    visibility: hidden;
  }
}

