.product {
    width: 100%;
    transition: all 0.3s ease-in-out;

    &-special-block{
        display: flex;
        flex-direction:column;
        align-items: center;

        &__wrap{
            max-width: 450px;
            @media(max-width: 574px){
                max-width: 100%;
            }
        }
    }
    
    &-label{
        &-wrap{
            position: absolute;
            right: 0;
            top: 30px;
            list-style: none;
            z-index: 1;

            @media( max-width: 574px){
                top: 17px;
            }

            @media( max-width: 400px){
                top: 14px;
            }
        }

        &__item{
            background: $black;
            color:$white;
            text-transform: uppercase;
            padding:  6px 20px;
            margin-bottom: 6px;
            font-weight: 500;

            @media( max-width: 574px){
                font-size: 12px;
                padding:  6px 16px;
            }
        }
    }

    &-file-block {
        display: flex;
        width: 100%;
        justify-content: center;
        align-items: center;
        flex-direction:column;
    }

    &-download{
        
        &-btn{
        width: 300px;
        height: 40px;
        font-size: 12px;
        padding-left: 55px;
        padding-right: 20px;
        background: #E5E5E5;
        position: relative;
        display: flex;
        align-items: center;
        margin-bottom: 16px;
        font-weight: 500;

        @media(max-width: 574px){
            width: 100%
        }

        &:before{
            content:'';
            background: url('../img/icon/file.svg') center center no-repeat $black;

            width: 40px;
            height: 40px;
            position: absolute;
            left: 0;top: 0;
        }
    }
    &-block{
        display: flex;
        flex-direction:column;
        align-items: center;
        text-transform: uppercase;
        width: 100%;
    }
    }

    &-block {

        & [class*="col-"] {
            padding: 0 2px ;

            @media(min-width: 768px)and(max-width: 991px){
                &:nth-child(3n){
                    padding-right: 0;
                }

                &:nth-child(3n+1){
                    padding-left: 0;
                }
                &:nth-child(2n){
                    padding: 0 1px;
                }
            }

        }

        & .col-12{
            @media(min-width: 400px) and (max-width: 574px){
                flex: 0 0 50%;
                max-width: 50%;
            }
    }


        &-title{
            @extend .product-info-title;
            display: flex;
            justify-content: center;
            width: 100%;
            margin-top: 72px;
            margin-bottom: 40px !important;
        }

    }

    &-wrapper {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        width: 100%;
    }

    &-img {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 350px;
        overflow: hidden;
        position: relative;


        &__wrapper{
            display: flex;
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
        }

        & img {
            max-width: 100%;
            width: auto;
            height: auto;
            max-height: 100%;
        }

        
    }

    &-content-block{
        margin: 30px 0;
        margin-bottom: 200px;

        @media(min-width:992px) and (max-width: 1200px){
            margin: 15px 0;
            margin-bottom: 200px;
        }

        @media(max-width:991px){
            margin: 0;
            margin-bottom: 100px;
            & .container{
                padding: 0;
            }

            & .row{
                margin: 0;
            }

            & [class*="col-"]{
                
                &:first-child{
                    padding: 0;
                }
            }
        }
        @media(min-width: 575px) and (max-width:767px){
            margin-bottom: 60px;
        }
        @media(max-width:574px){
            margin-bottom: 40px;
        }

    }

    &-info{
        &-section{
            padding-left: 5%;
            padding-top: 10%;
            position: relative;
            display: flex;
            flex-direction: column;

            @media(max-width:991px){
                padding-top: 28px;
            }

            @media(min-width: 992px) and(max-width: 1100px){
                padding-top: 40px;
                padding-left: 3%;
            }
            @media(max-width: 992px){
                padding-left: 0;
            }

            
        }

        &-action{
            width: 100%;
            display: flex;
            justify-content: flex-start;
            align-items: center;

            @media(max-width: 992px) and (min-width: 1200px){
                margin-bottom: 5%;
            }

            @media(min-width: 992px) and(max-width: 1100px){
                
                align-items: flex-end;
            }

            @media(max-width: 574px){
                flex-direction: column;
                align-items: stretch;
            }

            & .primary{
                margin-bottom: 0;
            }

            
        }

        &-subtitle{
            display: flex;
            font-size: 22px;
            font-weight: 300;

            @media (min-width: 992px) and (max-width: 1200px){
                font-size: 18px;
            }

            & * {
                &:after{
                    content: '|';
                    display: flex;
                    width: 4px;
                    margin: 0 10px;
                }

                &:last-child{
                    color: $black;
                    &:after{
                    content: none;
                    }
                }
            }
        }

        &-title{
            display: flex;
            width: 100%;
            line-height: 48px;
            font-size: 26px;
            font-weight: 500;
            line-height: 30px;
            margin-bottom: 15px;

            @media(max-width:1100px){
                font-size:26px;
            }

            @media(max-width:768px){
                font-size:22px;
                line-height: 26px;
            }

            @media(max-width:574px){
                font-size:20px;
                line-height: 24px;
            }
        }

        &-color{
            display: flex;
            color: $black;
            font-size:inherit;
            font-weight:inherit;
        }

        &-sku{
            display: flex;
                color: $black;
                font-size:inherit;
                font-weight:inherit;

                font-family: Montserrat;
                font-size: 14px;
                font-weight: 400;
                line-height: 17px;

        }

        &-block{
            width: 100%;
            display: flex;
            flex-direction: column;
            margin-bottom: 10%;

            @media(min-width: 992px) and(max-width: 1100px){
                margin-bottom: 35px;
            }
        }

        &-table{
                width: 100%;
                font-weight: 500;
                font-size: 14px;

                    @media(min-width: 992px) and (max-width: 1200px){
                        font-size: 14px;
                    }

                    & td{
                        display: flex;
                        align-items: center;
                        width: 45%;


                        @media(max-width: 992px){
                            &:first-child{
                                width: 40%;
                            }
    
                            &:last-child{
                                width: 60%;
                            }
                        }

                        @media(max-width: 574px){
                            &:first-child{
                                width: 50%;
                            }
    
                            &:last-child{
                                width: 50%;
                            }
                        }

                        

                        & a{
                            font-size: inherit;
                            font-size: 14px;
                            font-style: normal;
                            line-height: 17px;
                        }                        
                    }

                    & ul{
                        list-style: none;
                        display: flex;

                        & li {
                            margin-right: 20px;
                            position: relative;

                            &:last-child{
                                margin-right: 0;
                            }

                            &.active{
                                & a{
                                    font-weight: 500;
                                }

                                &:not(.product-info-pallete__item):after{
                                content: '';
                                display: flex;
                                height: 2px;
                                width: 110%;
                                position: absolute;
                                bottom: -2px;
                                left: -1px;
                                background: $black;
                                }

                            }
                            
                        }
                    }

                    & tr{
                        display: flex;
                        margin-bottom: 10px;

                        &:first-of-type{
                         

                            @media(max-width: 574px){
                                & .product-info-table__props{
                                    padding-left: 26px;
                                }
                            }
                        }
                    }

                   

                    &__value{
                        font-weight: 500;
                        
                    }

                    &__props{
                        font-size: 11px;
                        font-style: normal;
                        font-weight: 500;
                        line-height: 17px;
                        color: #afafaf;
                        letter-spacing: .4px;
                        text-transform: uppercase;
                    }


        }

        &-pallete{
            display: flex;

            &__wrapper{
            display: flex;
            justify-content: flex-start;
            align-items: center;
            list-style: none;
            margin-bottom: 5%
            }

            &__item{
                width: 24px;
                height: 24px;
                border-radius: 50%;
                overflow: hidden;
                margin-right: 10px;
                border: 1px solid rgba(0, 0, 0, 0.09);
                padding: 3px;
                transition: all .3s ease-in-out;

                & img{
                    border-radius: 50%;
                }

                &:last-child{
                    margin-right: 0;
                }

                &.active{
                    border: 1px solid $black;
                    &::after{
                        content:none;
                    }
                }

                &:hover {
                    border-color: $black;
                    transition: all .3s ease-in-out;
                }

                
            }

            &__img{
                width: 100%;
                height: 100%;
                border-radius: 50%;
            }
        }

        &-price{
            display: flex;
            align-items:center;
            justify-content: space-evenly;
            width: 45%;
            font-weight: 400;
            padding: 0 2%;

            @media(max-width: 991px){
                width: 40%;
            }
            @media(max-width: 574px){
                width: 100%;
                justify-content: flex-start;
            }

            @media(min-width: 992px) and(max-width: 1100px){
                flex-direction: column;
                align-items: flex-start;
                width: 35%;
            }
            
            &__new{
                display: flex;
                font-weight: inherit;
                font-size: 26px;
                font-style: normal;
                font-weight: 500;
                line-height: 27px;

                @media(max-width: 574px) {
                    font-size: 22px;
                }

                &-currency{
                    margin-left: 4px;
                    font-size: 18px;
                    font-style: inherit;
                    font-weight: inherit;
                    line-height: 32px;

                    @media(max-width: 574px) {
                        font-size: 16px;
                    }
                }
            }

            &__old{
                display: flex;
                align-items: flex-end;
                color:$red;
                font-size: 20px;
                font-style: normal;
                font-weight: 500;
                line-height: 27px;
                position: relative;
                margin-right: 4%;

                @media(min-width: 992px) and(max-width: 1100px){
                    margin-bottom: 12px;
                }
                @media(max-width: 574px) {
                    font-size: 18px;
                }
                

                &:before{
                    content:'';
                    width: 100%;
                    height: 1px;
                    background: $red;
                    position: absolute;
                    top: 50%;
                }
                
                &-currency{
                    margin-left: 4px;
                    font-size: 14px;
                    font-style: inherit;
                    font-weight: inherit;
                    line-height: 20px;
                }


            }
        } 

        &-status{
            margin-right: 30px;
        }

        &-description{
            font-weight: 400;
            letter-spacing: 1px;
            font-size: 16px;

            @media(min-width: 992px) and (max-width: 1200px){
                font-size: 14px;
            }

            &:after {
                content: '+';
                font-size: 13px;
                color:$lightgreen;
                float: right;
                margin-left: 5px;
                font-size: 24px;
            }
        
            &:before{
                content:'';
                width: 100%;
                height: 1px;
                background: $lightgreen;
                position: absolute;
                bottom: 10px;
                left: 0;
            }
        
            &.active {
        
                &:after {
                    content: "-";
                    color:$black;
                    font-size: 24px;
                }
        
            }
        }

        &__btn-block{
        display: flex;
        width: 50%;
        & .primary{
            width: 100%;
        }
        
        @media (max-width: 1100px) and (min-width: 992px){
            width: 65%;
        }

        @media(max-width: 574px){
            width: 100%;
            
            margin-top: 10px;
            
        }
        }
    }

    &-pallete {
        &__wrapper {


            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: flex-start;
            width: 100%;
            padding: 10px 0;
            position: absolute;
            left: 0;
            bottom: 0;
        }
        margin-left: 10px;
        list-style: none;
        display: flex;
        flex-wrap: wrap;

        &__item {
            width: 30px;
            height: 30px;
            margin-right: 5px;
            overflow: hidden;
            border-radius: 50%;
            padding: 2px;
            border: 1px solid $lightgrey;
            transition: all 0.3s ease-in-out;

            & img {
                width: 100%;
                border-radius: 50%;
            }
            & a {
                width: 100%;
                height: 100%;
                display: flex;
            }

            &:last-child {
                margin-right: 0;
            }

            &:hover {
                border-color: $black;
            }
        }
    }

    &-buy {
        width: calc(100% - 20px);
        background: $black;
        border-radius: 4px;
        padding: 8px 16px;
        text-transform: uppercase;
        color: $white;
        margin: 10px;
    }

    &-gallery {
        @extend .swiper-container;
        &__wrapper {
            @extend .swiper-wrapper;
            display: flex;
            align-items: center;
        }

        &__item {
            @extend .swiper-slide;
            width: 100%;
            display: flex;

            & img{
                display: flex;
                justify-content: center;
                align-items:center;
                width: 100%;
            }            

            &.active{
                position: relative;
            }
            }

        &-container{
           position: relative;
        }

        &-button{
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            display: flex;
            z-index: 1;
            transition: all .3s ease-in-out;

            &-prev{
                left: 0;
                width: 40px;
                font-size: 0;
                &-arrow{
                    fill:$white;
                    opacity: .5; 
                }
    
                &:hover{
                    & .product-gallery-button-prev-arrow{
                        opacity: 1;
    
                    }
                }
            }
            
            &-next{
                right: 0;
                width: 40px;
                font-size: 0;
                &-arrow{
                    fill:$white;
                    opacity: .2; 
                    transform: scale(-1, -1);
                }
    
                &:hover{
                    & .product-gallery-button-next-arrow{
                        opacity: 1;
    
                    }
                }
  
            }
        }

        &-thumbs {
            @extend .swiper-container;
            position: absolute;
            left: 30px;
            bottom: 30px;
            width: 300px;
            overflow: hidden;
            margin-left: 10px;
            display: flex;

            @media(max-width: 991px){
                left: 5px;
                bottom: 15px;
            }

            @media(max-width:991px){
                display: none;
            }

            &__wrapper {
                @extend .swiper-wrapper;
            }

            &__item {
                @extend .swiper-slide;
                background:$white;
                max-width: 50px;
                min-height: 50px;
                display: flex;
                justify-content: center;
                align-items:center;   
                border-radius: 50%;     
                overflow: hidden;
                border:1px solid $lightgrey;

            }
                    &-active{
                        border:1px solid $black;
                }

        }
    }

    &-category{
        &-img{
            position: relative;
        }
            &-slider{
            @extend .swiper-container;
            position: relative;

            &-price{
                margin-top: 15px;
                margin-bottom: 50px;
                @media(max-width: 991px){
                    margin-bottom: 40px;
                }
                @media(max-width: 767px){
                    margin-bottom: 70px;
                }
                &__old{
                    color:$red;
                    font-size: 16px;
                    line-height: 20px;
                    position: relative;
                    margin-right: 20px;
                   

                    &-currency{
                        font-size: 14px;
                        line-height: 17px;
                    }

                    &:after{
                        content: '';
                        position: absolute;
                        left: 0;
                        background: $red;
                        height: 1px;
                        width: 100%;
                        top: 50%;
                        display: flex;
                    }
                }

                &__new{
                    font-size: 20px;
                    font-weight: 500;
                    line-height: 27px;

                    &-currency{
                    font-size: 15px;
                    font-weight: 500;
                    line-height: 20px;
                    }


                }
            }

            &__wrapper {
                @extend .swiper-wrapper;
                display: flex;
                align-items: flex-end;
                height: 100%;
            }

            &__item {
                @extend .swiper-slide;
                width: 100%;
                display: flex;
                background: #fff;
                transition: opacity .25s ease-in-out;
            }

            &__link{
                display: flex;
                flex-direction: column;
                width: 100%;
                height: 100%;
            }

            &__img-wrap{
                display: flex;
                justify-content: center;
                align-items: flex-start;
                width: 100%;
                margin-bottom: 10px;
            }
            &-container{
                position: relative;
            }
            
            &-thumbs {
                @extend .swiper-container;
                position: absolute;
                left: 7px;
                bottom: 54px;
                width: calc(100% - 14px);
                overflow: hidden;
                display: flex;
                z-index: 5;

                &__wrapper {
                    @extend .swiper-wrapper;
                    padding: 4px;
                }

                &__item {
                    @extend .swiper-slide;
                    max-width: 26px;
                    min-height: 26px;
                    display: flex;
                    justify-content: center;
                    align-items:center;   
                    border-radius: 50%;     
                    overflow: hidden;
                    padding: 2px;
                    border: 2px solid transparent;
                    transition: all 0.4s ease-in;
                    cursor: pointer;

                    &:hover, &:focus{
                        box-shadow: 0 0 0 3px rgba(99,96,96,0.15);
                        transition: all 0.3s ease-in;
                        
                    }
                    

                    

                    & div{
                        display: flex;
                        width: 18px;
                        height: 18px;
                        border-radius: 50%;
                    }
                }
                        &-active{
                            border-color: rgba(99,96,96,0.6);
                    }

            }
        }
        &-wrap{
            position: relative;
            & [type="checkbox"]{
                display: none;
                position: absolute;
                left: 12px;
                top: 12px;
                z-index: 7;
            }
        }
    }

    &-favorite{

        &-block{
            @extend .product-block;
        }

        &__item{
            & [type="checkbox"]{
                visibility: hidden;
                opacity: 0;
                position: absolute;
                left:12px;
                top: 12px;
                transition: all .3s ease-in-out;

                &.visible{
                    visibility: visible;
                    opacity: 1;
                    transition: all .3s ease-in-out;
                }
            }
        }

        &-empty{
            @extend .container;
            display: flex;
            justify-content: center;
            align-items:center;
            min-height: 250px;
            position: relative;
            &:after{
                content: "";
                width: 76%;
                background: $lightgrey;
                height: 1px;
                position: absolute;
                left: 12%;
                bottom: 0;
            }
        }

        &__img{
            &-wrap{
                @extend .product-category-slider__img-wrap;
            }
        }

        &-price{
            @extend .product-category-slider-price;

            &__new{
                @extend .product-category-slider-price__new;
                &-currency{
                    @extend .product-category-slider-price__new-currency;
                }
            }

            &__old{
                @extend .product-category-slider-price__old;
          
                &-currency{
                    @extend .product-category-slider-price__old-currency;
                }
            }
        }
        
    }

    &-properties{
        &-block{
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items:center;
        }

        &-title{
            text-transform: uppercase;
            font-size: 34px;
            font-weight: 400;
        }

        &-table{
            display: flex;
            width: 100%;
            flex-direction: column;
            align-items: center;          

            & tbody{
                display: flex;
                flex-direction: column;
                margin-bottom: 60px;
                width: 100%;
                max-width: 450px;

                @media(max-width: 574px){
                    max-width: 100%;
                }

                &:last-child{
                    margin-bottom: 0;
                }

                & tr{
                    display: flex;
                    margin-bottom: 10px;
                    justify-content: space-between;
                    align-items: flex-start;
                    

                    & * {
                        width: calc(50% - 5px);
                        margin-right: 90px;

                        @media(max-width: 575px){
                            margin-right: 10px;
                        }

                        &:last-child{
                            margin-right: 0;
                        }
                    }
                }
            }

            &__props{
            @extend .product-info-table__props;

            }

            &__value{
                display: flex;
                font-size: 14px;
                font-style: normal;
                font-weight: 400;
                line-height: 17px;


                &::first-letter{
                    text-transform: uppercase;
                }

            }
        }
    }

    &-remove{
        position: relative;
        width: 30px;
        height: 30px;
        background-image: url("./../img/close.svg");
        background-repeat: no-repeat;
        background-position: center center;
        background-size:28px 28px;
    }

    &-price{
        display: flex;
        &-new, &-old{
            display: flex;
            justify-content: center;
            align-items:flex-end;

            &__value{
                font-size: 32px;
                font-weight: 400;
            }
    
            &__currency{
                font-size: 24px;
                font-weight: 400;
                line-height: 15px;
                margin-left: 5px;
            }
        }

        &__old{
            position: relative;
            &::after{
                content: '';
                width: 100%;
                height: 1px;
                background:red;
                position: absolute;
                left: 0;
                top: 50%;
            }
        }      
    }

    &-quantity{
        display: flex;

        background: $lightgrey;
        padding: 1px;
        font-weight: 400;   
        font-size: 16px!important;

        &-decrement,&-increment{
            padding: 7px 9px;
            font-size: inherit;
            font-weight: inherit;
        }

        

        &-value{
            display: flex;
            justify-content: center;
            align-items:center;
            width: 40px;
            padding: 0;
            text-align: center;
            font-size: inherit;
            font-weight: inherit;
            background: none;
            border: none;
            outline: none;
        }
    }
}
