/* Modal Header */
.modal{
  position: fixed;
  z-index: 20;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  background:rgba(0,0,0,0.2);
  visibility: hidden;
  opacity: 0;
  transition: all .2s ease-in-out;

  &.active{
    visibility: visible;
    opacity: 1;
    transition: all .2s ease-in-out;

    & .modal-content {
      animation-name: animatetop;
      animation-duration: 0.8s;
    }
  }
  

  &-header {
    position: relative;
      padding: 20px 16px;
      display: flex;
      justify-content: center;
      align-items: center;
      text-transform: uppercase;
      flex: 1 0 auto;
      height: 100%;
      background: $white;
      max-height: 70px;


  
      & h1,h2,h3,h4,h5,h6{
        font-size: 16px;
        font-weight: 400;
        }
  
      & .close{
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        top:50%;
        transform: translateY(-50%);
        right: 10px;
        width: 30px;
        height: 30px;

        & svg {
          width: 18px;
          height: 18px;
        }

        & > *{
          pointer-events: none;
        }
        
      }
    }

    &-body {
      padding: 2px 15px;
      overflow: auto;
      max-height: 70vh;
      flex: 1 0 auto;
      background-color: $white;

      @media (max-width:991px){
        max-height: calc(100vh - 150px);
      }


    }

    &-footer {
      padding: 20px 16px;
      background-color: $white;
      display: flex;
      justify-content: center;
      align-self: center;
      border-top: 1px solid $lightgrey;
      flex: 0 0 auto;
      height: 100%;
      max-height: 90px;
      width: 100%;
  
      & button{
        width: 100%;
        margin-right: 10px;
        height: 45px;
        order: 2;
        font-size: 14px;
        font-weight: 500;
        display: flex;
        justify-content: center;
        align-items:center;

        @media(max-width: 574px){
          max-width: 100%;
          width: 100%;
        }
  
        &:last-child{
          margin-right: 0;

          @media(max-width: 574px){
            order: 1;
          }
        }
      }

      @media(max-width: 574px){
        flex-direction: column;
        max-height: 130px;
        padding-bottom: 0;
      }
    }

    &-content {
      position: relative;
      background-color: $white;
      margin: auto;
      padding: 0;
      width: 100%;
      max-width: 700px;
      box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);
      display: flex;
      flex-direction: column;
      

      @media(max-width: 991px){
        height: 100vh;
        max-width: 100%;
        min-height: 100%;
      }
    }

    &-info{
      &-angle, &-temperature{
        margin-bottom: 60px;
        & img{
          margin-bottom: 15px;
        }
        & span{
          display: flex;
          font-weight: 500;
          margin-bottom: 10px;
        }
      }

      &-title{
        position: relative;
        display: flex;
        align-items:center;
        min-height: 80px;

        &::before{
          content:'';
          bottom:0;
          left: 0;
          display: flex;
          position: absolute;
          width: 100%;
          height: 1px;
          background: #f3f3f3;
        }
      }
      &-content{

        & strong{
          font-weight: 500;
        }

        & p{
          margin-bottom: 30px;
        }

        & ol{
          counter-reset: section -1;                             
          list-style-type: none;
          margin-top: 95px;

          & li{
            margin-bottom: 16px;
            &::before {
              counter-increment: section;           
              content: counters(section,"") " - ";
              font-weight: 500;
              font-size: 14px;
            }
          }
        }


      }
    }

}

#phoneModal{

  & .modal-header{
    justify-content: flex-start;
    background: $white;
  }

  & .modal-content{
    max-width: 500px;
    max-height: 500px;

    @media(max-width: 991px){
      max-width: 100%
    }
  }
  .email,.call,.schedule{
    margin-bottom: 40px;
    font-size: 14px;
    font-weight: 400;
    
    ul{
      list-style: none;
    }

    h2{
      font-size: 14px;
      margin-bottom: 20px;  
    }

    a{
      color: #787878;
      text-transform: none;
    }
  }
  .call{
    margin-top: 30px;
    ul{
      color: #787878;
    }

  }
  .schedule{
    
      & :last-child{

        ul{

          color:$red;
        }
      }

  }


}

#filterModal{
  & .modal{
    &-header{
      background: #f3f3f3;

      & h1,h2,h3,h4,h5,h6{
      font-size: 16px;
      }

      .close svg {
        width: 18px;
        height: 18px;
      }
    }
  }
}

#modalInfo{
  & .modal{
    &-header{
      justify-content: flex-start;
    }
    &-body{
      padding-top: 20px;
      max-height: calc(100vh - 70px);
    }
  }
}
