.filters{
    &-block{
        padding: 30px 0;

        @media(max-width: 991px){
            padding: 30px 0 5px;
        }

        & [class*="col-"] {
            padding: 0 5px;
        }

        & .select:not(.sort){
            display: flex;
            width: 100%;
            margin-bottom: 6px;
            min-height: 45px;
        }

        
    }

    &-download{
        display: flex;
        justify-content: center;
        align-items:center;
        font-weight: 500;

        & svg{
            width: 22px;
            height: 22px;
            margin-right: 10px;
            margin-bottom: 5px;
        }
    }

    &-settings{
        display: flex;
        justify-content: space-between;
        padding: 20px 0;

        @media(max-width: 767px){
            display: none;
        }
    }

    &-count-product{
        max-width: 260px;   
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: flex-start;

        & p{
            display: flex;
            width: 100%;
            max-width: 130px;
            min-width:130px;
            height: 100%;
            margin: 0;
            margin-right: 10px;
            align-items: center;
        }

        &.select{
            display: flex;
        }
    }

    &-reset{
        display: flex;
        justify-content: flex-end;
        align-items: center;
        color: $black;
        font-weight: 500;   
        min-height: 45px;  
        width: 100%;   
    }

}

.filter{
    padding:20px 0;
    border: none;

    & .select{
        margin-bottom: 20px;
    }

    &:last-child{
        border-bottom: none;
    }

    &-content{
        display: flex;
        flex-wrap: wrap;
    }

    &-item{
        width: 33%;
        padding:10px 5px;
        display: flex;
        align-items: flex-start;
       


        & .label{
            cursor: pointer;
            color: #787878;
            margin: 0;
        }

        & input{
            margin-right: 3px;
        }

        @media(max-width: 991px){
            width: 50%;
        }

        @media(max-width: 574px){
            width: 100%;
        }
    }

    &-title{
        margin-bottom: 15px;
        padding: 0 5px;
        & lagend{
        font-weight: 500;
        font-size: 16px;
    }
}

}