.category{
    &-nav{
        
        background:$lightgrey;
        max-height: 120px;
        display: flex;
        align-items: center;
        position: relative;

        @media(min-width:768px) and (max-width: 991px){
            max-height:100px;
        }

        @media(max-width: 575px){
            display: none;
            max-height: 80px;
        }

        &__wrapper{
            @extend .swiper-wrapper;
            display: flex;
            // justify-content: center;
            // @media(max-width: 768px){
            //     justify-content: flex-start;
            // }
        }

        &__slider{
            @extend .swiper-container;
            width: 100%;
        }

        &__item{
            @extend .swiper-slide;
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
            
            

            &.active{
                &:before{
                    content:'';
                    position: absolute;
                    display: flex;
                    height: 2px;
                    left: 0;
                    bottom: 0px;
                    width: 100%;
                    background:$black;
                    opacity: .5;
                }
            }

            &:hover{
                &:before{
                    content:'';
                    position: absolute;
                    display: flex;
                    width: 100%;
                    height: 2px;
                    left: 0;
                    bottom: 0px;
                    background:$black;
                    opacity: .2;
                }
            }
        }

        &__link{
            padding: 0 20px;
            height: 120px;
            display: flex;
            justify-self: center;
            align-items: center;
            text-align: center;
            font-size: 12px;
            text-transform: uppercase;

            @media(min-width:768px) and (max-width: 991px){
                height:100px;
            }
    
            @media(max-width:767px){
                
                height: 80px;
            }
        }
    }
}