.header {

    width: 100%;
    height: 100%;
    z-index: 6;
    transition: all .6s ease-in-out;
    // transition-delay: 0.2s;

    
    @media(max-width: 991px){
        border-bottom: 1px solid $lightgrey;
        position: relative;
        background: $white;
        position: sticky;
        top: 0;
    }

    &-content {
        // z-index: 3;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 80px;
        position: relative;

        &-wrapper {
            display: flex;
            justify-content: space-between;
            align-items: center;
            height: 100%;
            z-index: 2;
            height: 80px;

            @media(max-width: 767px){
                height: 55px;
            }
            
            @media(max-width: 574px){
                height: 50px;
            }
        }

        &__wrap{
            position: absolute;
            left: 0;
            bottom: 100%;
            width: 100%;
            background-color: transparent;
            transition: all .5s ease-in-out;
            transform: translate(0, 100%);
        }

        @media(max-width: 991px){
            background:$white;
        }

        @media(max-width: 767px){
            height: 55px;
        }

        @media(max-width: 574px){
            height: 50px;
        }
    }
    & .logo {

        & img, svg{
            max-width: 180px;
            width: 100%;

            @media(min-width:768px) and (max-width:1200px){
                max-width: 170px;
            }

            @media(min-width: 575px) and (max-width:767px){
                max-width: 150px;
            }

            @media(max-width: 574px){
                max-width: 120px;
            }

        }

        @media (min-width: 923px) {
            display: flex;
        }
    }

    &-nav {
        position: absolute;
        left: 0;
        display: flex;
        height: 100%;
        width: 100%;
        display: flex;
        justify-content: space-between;

        &.show {
            // animation: show 0.6s ease-in-out;
        }

        &.hide {
            //animation: hide 0.6s ease-in-out;
        }

        &-wrapper {
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            height: 100%;
            width: 100%;
            margin-left: 5%;
            max-width: 800px;

            @media(max-width: 991px) {
                width: 36px;
                margin-left: 0;
            }

        }
    }

    &-list {
        list-style: none;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 0;
        padding: 0;
        width: 100%;

        &__item {
            // padding: 0 5px;
            text-transform: uppercase;
            font-weight: 400;
            // letter-spacing: 1px;
            font-size: 14px;
            color: inherit;
            display: flex;
            justify-content: center;
            align-items: center;
            height: 100%;
            transition: all .2s ease-in-out;
            &:hover{
                cursor: pointer;
            }

            a{
                font-size: inherit;
                font-weight: inherit;
            }

            &:first-child{
                padding: 0;
            }

            & .marker{
                pointer-events: none;
                position: absolute;
                display: flex;
                justify-content: center;
                align-items:center;
                right: 2px;
                bottom:0px;
                border-radius: 50%;
                padding: 0;
                padding-left: 1px;
                padding-right: 1px;
                background: $black;
                overflow: hidden;
                width: 14px;
                height: 14px;
 
                
                & span{
                    margin-left: 2px;
                    font-weight: 400;
                    font-size:7px;
                    line-height: 0;
                    color: $white;
                    padding: 2px;
                }
            }

            &.triangle{
                position: relative;
                &:before{
                    content: "";
                    position: absolute;
                    right: calc(50% - 10px);
                    top:calc(100% - 7px);
                    display: flex;
                    width: 0;
                    height: 0;
                    opacity: .4;
                    border-color: transparent transparent $white;
                    border-style: solid;
                    border-width: 0 7px 7px;
                    @media(max-width: 991px){
                        border-color: transparent transparent $white;
                        opacity: 1;
                        top:calc(100% - 6px);
                    }
                }

            }

            @media(max-width: 991px) {
                &:not(:last-child) {
                    display: none;
                }
            }
        }
    }

    &-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 25px;
        height: 25px;
        position: relative;
        outline: none;

        & svg{
            width: 18px;
            height: 18px;
        }

        &-group {
            display: flex;
            flex-direction: column;
            position: relative;
            overflow: hidden;

            & > .show {
                top: 0;
                animation: show 0.3s ease-in-out;
                z-index: 1;
            }

            & > .hide {
                animation: hide 0.3s ease-in-out;
            }
        }

    }

    &-burger {
        position: relative;
        display: none;
        z-index: 2;
        width: 100px;

        @media(max-width: 991px) {
            display: flex;
        }

        &.active {

            & span {
                transition: all 0.3s ease-in-out;
                left: 7px;
                &:first-child {
                    transform: rotate(45deg);
                    top: 7px;
                }
                &:last-child {
                    transform: rotate(-45deg);
                    bottom: 9px;
                }
            }
        }

        & span {
            display: flex;
            position: absolute;
            width: 17px;
            height: 2px;
            left: 5px;
            border-radius: 4px;
            background: $black;
            transform-origin: 0 50%;
            transition: all 0.3s ease-in-out;

            &:first-child {
                top: 8px;
            }
            &:last-child {
                bottom: 8px;
            }
        }
    }

    &-search {
        display: flex;
        border: none;
        width: 100%;
        height: 60%;
        border-radius: 0;
        appearance: none;
        -moz-appearance: none;
        -webkit-appearance: none;
        outline: none;
        padding-right: 34px;

        &-menu {
            background: #f3f3f3;
            min-height: 35px;
            width: calc(100% - 100px);
            margin: 10px 50px;
            padding: 8px 16px;
            border-color: #f3f3f3;

            @media(max-width: 425px){
                margin: 10px 30px;
                width: calc(100% - 60px);
                min-height: 38px;
            }
        }

        &__wrapper {
            display: none;
            //position: absolute;
            align-items: center;
            justify-content: center;
            left: 0;
            width: 100%;
            height: 100%;
            background: $white;
            z-index: 1;
            padding: 0;
            transform: translate(0, -100%);
            -webkit-transition: -webkit-transform 800ms cubic-bezier(0.4,0,0.2,1);
            transition: transform 800ms cubic-bezier(0.4,0,0.2,1);

            &.show {
                -webkit-transition: -webkit-transform 800ms cubic-bezier(0.4,0,0.2,1);
                transition: transform 800ms cubic-bezier(0.4,0,0.2,1);
                transform: translate(0, 0);
            }
            &.hide{
                -webkit-transition: -webkit-transform 800ms cubic-bezier(0.4,0,0.2,1);
                transition: transform 800ms cubic-bezier(0.4,0,0.2,1);
                transform: translate(0, -100%);
            }
        }

        &__label {
            margin-left: 0;
            font-weight: 400;
            text-transform: uppercase;

        }

        &__form-btn{
            display: flex;
            justify-content: space-between;
            width: 12%;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 4px;

        }
    }

    &-tab {
        &__wrapper {
            background: $white;
            position: absolute;
            width: 100%;
            left: 0;
            top:100%;
            height: 0;
            transition: all .5s ease-in-out;
            box-shadow: 0 2px 16px 0 rgba(5,5,5,0);
            overflow: hidden;
            display: flex;
            // animation: hideBasketHeader .5s ease-in-out;

            z-index: -1;
            // -webkit-transition: box-shadow 400ms cubic-bezier(0.4,0,0.2,1),-webkit-transform 600ms cubic-bezier(0.4,0,0.2,1);
            //     transition: box-shadow 400ms cubic-bezier(0.4,0,0.2,1),transform 600ms cubic-bezier(0.4,0,0.2,1);

            @media(max-width: 991px){
                display: none;
            }

            &.show {
                box-shadow: 0 2px 16px 0 rgba(5,5,5,0.2);
                
                height: 750px;
                
                // -webkit-transition: box-shadow 400ms cubic-bezier(0.4,0,0.2,1),-webkit-transform 800ms cubic-bezier(0.4,0,0.2,1);
                // transition: box-shadow 400ms cubic-bezier(0.4,0,0.2,1),transform 800ms cubic-bezier(0.4,0,0.2,1);
                // transform: translate(0, calc(100% + 70px));
                z-index: 1;
                // @media(max-width: 767px){
                //     transform: translate(0, calc(100% + 55px));
                // }
        
                // @media(max-width: 574px){
                //     transform: translate(0, calc(100% + 50px));
                // }
            }
        }

        &-content {
            @extend .container;
            margin-bottom: 20px;

            &__nav {
                display: flex;
                animation: fadeOut 1s;
                &.active {
                    animation: fadeIn 1s;
                }
            }

            &__list {
                width: 100%;
                display: flex;
                justify-content:flex-start;
                align-items: flex-start;
                flex-wrap: wrap;
                list-style: none;

                &.hide{
                    display: none;
                }
            }

            &__item {
                width:calc(25% - 10px);
                display: flex;
                flex-direction: column;
                margin-right: 10px;
                margin-bottom: 10px;
                margin-top: 50px;
                opacity: 0;
                cursor: pointer;
                

                &:nth-child(4n){
                    margin-right: 0;
                }

                &.animation {
                    animation-name: fadeIn;
                    animation-duration: .5s;
                    animation-delay: .3s;
                    animation-direction: normal;
                    animation-timing-function: ease-in-out;
                    animation-fill-mode: forwards;
                    
                }
            }

            &__link {
                width: 100%;
                max-height: 250px;
                overflow: hidden;
                text-transform: uppercase;
                display: flex;
                justify-content: flex-start;
                align-items: flex-start;
                flex-direction: column;
                position: relative;
                margin: 0;
                color: $black;
                cursor: pointer;
            }

            &__title {
                margin-top: 10px;
                padding: 5px 0 10px 0;
                display: flex;
                justify-content: flex-start;
                width: 100%;
                font-weight: inherit;
                color: inherit;
            }

            &__img {
                //height: 100%;
                //max-height: 200px;
                width: 100%;
                display: flex;
                justify-content: center;
                align-items:center;
            }
        }
    }

    &-lang{
        padding: 0 4px;
        &:first-child{
            position: relative;
            &::after{
                content: '';
                display: flex;
                background: $black;
                height: 100%;
                width: 1px;
                position: absolute;
                right: 0;
                top: 0;
            }
        }
    }

}

    

#header-search-btn {
    top: 0;
    @media(max-width: 991px) {
        display: none;
    }
}

#burger {
    top: 0;
}

#header-search-reset {
   
    background-size: 25px;


    @media(max-width: 991px) {
        display: none;
    }
}

#header-basket-btn, #burger{

    @media(max-width:993px){

        width: 30px;
        height: 30px;
    }
}

#header-basket-btn{
    background-repeat: no-repeat;
    background-position: center center;  
}

#header-phone{
    background-repeat: no-repeat;
    background-position: center center;
  
}




