.basket {

    &-aside {
        visibility: hidden;
        opacity: 0;
        background-color: #fff;
        backface-visibility: hidden;
        flex-direction: column;
        display: flex;
        right: -100%;
        top: 0;
        max-width: 320px;
        overflow-x: hidden;
        overflow-y: auto;
        position: absolute;
        height: fit-content;
        color: $black;
        padding: 10px;
        -webkit-transition: box-shadow 400ms cubic-bezier(0.4,0,0.2,1),-webkit-transform 0.8s cubic-bezier(0.4,0,0.2,1);
        transition: box-shadow 400ms cubic-bezier(0.4,0,0.2,1),transform 0.8s cubic-bezier(0.4,0,0.2,1);
        width: 100%;
        will-change: transform;
        z-index: 175;
        -webkit-backface-visibility: hidden;
        -webkit-overflow-scrolling: touch;
        max-height: calc(100vh - 50px);
        transition: all .9s ease-in-out;
        z-index: 6;
        top:60px;
        

        @media(max-width: 991px){
            position: fixed;
            top:80px;
        }


        @media(min-width: 575px) and (max-width: 767px){
            top:55px;
        }

        @media(max-width: 574px){
            top:50px;
        }

        @media(max-width: 574px) {
            padding: 10px 15px;
            border-radius: 0 0 4px 4px;
            min-width: 100%;
            left: 0;
            top: -100vh;
            background: $white;
            border-radius: 0;
        }

        &__wrap {
            position: absolute;
            left: 0;
            top: 0;
            height: 100%;
            width: 100%;
            overflow: hidden;
            display: flex;
            transition: all .3s ease-in-out;
            z-index: 5;
            pointer-events: none;
            

            &.show{
                pointer-events: initial;
                @media(max-width: 991px){
                    transition: all .3s ease-in-out;
                    background:rgba(0,0,0, .2);
                    position:fixed;
                }
            }
        }

        &.show {
            -webkit-transition: box-shadow 400ms cubic-bezier(0.4,0,0.2,1),-webkit-transform 0.8s cubic-bezier(0.4,0,0.2,1);
            transition: box-shadow 400ms cubic-bezier(0.4,0,0.2,1),transform 0.8s cubic-bezier(0.4,0,0.2,1);
            box-shadow: 0 2px 16px 0 rgba(5,5,5,0.2);
            -webkit-box-shadow: 0 2px 16px 0 rgba(5,5,5,0.2);
            will-change: transform,width,background-color;
            visibility: visible;
            right: 10px;
            opacity: 1;
            visibility: visible;
            transition: all .9s ease-in-out;         
           

            @media(min-width: 992px) and (max-width: 1100px) {
                margin-bottom: 20px;
            }

            @media(min-width: 1300px) {
                right: calc(50% - 648px);
            }

            @media(max-width: 1298px) {
                right: 2px;
            }

            @media(max-width: 574px) {
                top: 50px;
            }
        }

        &-block {
            position: relative;

            @media(max-width: 767px) {
                position: initial;
            }
        }

        &__list {
            list-style: none;
            margin-bottom: 20px;
            margin-top: 20px;
        }

        &__item {
            color: $black;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            position: relative;
        }

        &__label {
            width: 18px;
            height: 18px;
            z-index: 2;
            display: flex;
            justify-content: center;
            align-items: center;
            fill: $black;
            position: absolute;
            top: 50%;
            left: 3px;
            transform: translate(20px, -50%);
        }

        &__link {
            display: flex;
            align-items: center;
            width: 100%;
            padding-left: 60px;
            font-size: 14px;
            line-height: 17px;
            font-weight: 400;
            min-height: 42px;

        }
        & .account{
            display: flex;
            justify-content: flex-start;
            align-items:center;
            width: 100%;
            margin-top: 4px;
            padding: 16px 20px;
            box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.15);

            & svg{
                width: 25px;
                height: 25px;
                margin-right: 16px;
            }

            & span{
                color: #000;
                font-size: 14px;
            }

            & p {
                font-size: 10px;
            }
        }
    }
}
