.architect{
    position: relative;    
    background: $black;

    &:after{
        content: '';
        width: 100%;
        height: 100%;
        display: flex;
        position: absolute;
        left: 0;
        top: 0;
    }

    &-content{
        @extend .ptb;
        display: flex;
        justify-content: space-around;
        position: relative;
        z-index: 1;
        min-height: 400px;

        @media(min-width: 768px) and (max-width: 991px){
                padding-top:55px;
                padding-bottom: 65px;
        }

        &-wrap{
            display: flex;
            width: 100%;
            @media(max-width: 992px){
                flex-direction: column;
                align-items: center;
                justify-content: center;
            }
        }

        &__subtitle{
            font-size: 16px;
            color: inherit;
            opacity: .8;
            font-weight: 300;
            line-height: 20px;
            letter-spacing: .1px;

            @media(max-width: 992px){
                font-size: 14px;
            }

            @media(min-width: 575px) and (max-width: 991px){
               width: 75%;
            }
        }

        &__text{
            display: flex;
            flex-direction: column;
            justify-content:center;
            padding-left: 5%;
            color: $white;
            max-width:  43%;

            & h2{
                line-height: 23px;
                color: inherit;
                margin-bottom: 50px;
                font-size: 28px;
                font-weight: 400;
                @media(max-width: 992px){
                    margin-bottom: 20px;
                }

                @media (min-width: 768px) and (max-width: 992px){
                    margin-bottom: 30px;
                    font-size: 24px;
                }
                @media (min-width: 575px) and (max-width: 767px) {
                    font-size: 22px;
                }

                @media(max-width:574px){
                    font-size: 19px;
                }


            }

            & .outline{
                margin-top: 50px;
                background-color: transparent;
                border: 1px solid $white;
                text-transform: uppercase;
                color: $white;
                font-weight: 500;
                padding: 10px 45px;
                display: flex;
                height: 45px;
                justify-content: center;
                transition: all .3s ease-in-out;
                align-items: center;
                max-width:230px;
                
                @media(min-width: 991px){
                    max-width: 45%;
                }

                @media(max-width: 450px){
                    max-width:100%;
                }

                &:hover{
                    background:$white;
                    color: $black;
                    transition: all .3s ease-in-out;
                }
            }


            @media(max-width: 992px){
                margin-top: 40px;
                max-width: 100%;
                padding-left: 0;
            }

            @media(max-width: 768px){
                margin-top: 30px;
            }

        }   

        &__img{
            display: flex;
            width: 100%;
            max-width: 700px;

            @media(max-width: 992px){
                max-width: 100%;
            }
        }
    }
}