.category{
    padding-left: 15px;
    padding-right: 15px;
    &-content{
        &__title{
            display: flex;
            justify-content: center;
            align-items: center;
            text-transform: uppercase;
            padding: 70px 0 90px;
            transition: all .3s ease-in-out;


            & h1{
                font-size: 34px;
            }
           

            @media(min-width: 768px) and (max-width:991px){
                padding: 60px 0 80px;

                & h1{
                    font-size: 28px;
                }
            }  

            @media(min-width: 575px) and (max-width:767px){
                padding: 40px 0 60px;

                & h1{
                    font-size: 24px;
                }
            } 

            @media(max-width: 574px){
                padding: 35px 0 40px;

                & h1{
                    font-size: 20px;
                }
            }
        }

        &-item{
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: flex-start;
            margin-bottom: 15px;

            &__img{
                width: 100%;
            }

            &__title{
                padding: 15px 0 40px 0;
                text-transform: uppercase;
            }
        }
    }
}