.checkbox, .radio{
    height: 16px;
    width: 16px;
    min-height: 16px;
    min-width: 16px;
    max-height: 16px;
    max-width: 16px;
    border-radius: 50%;
    border: 1px solid $black;
    appearance: none;
    -webkit-appearance: none;
    padding: 2px;
    position: relative;
    margin-bottom: -1px;
    cursor: pointer;
    outline: none;

    &::before {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
        padding: 4px;
        border-radius: 50%;
    }

    &:checked::before {
        background: $black;
    }

    &:disabled {
        border-color: #d8d8d8;
    }
}