.search{
    &-block{
        padding: 50px 0 30px 0;

        @media(max-width: 992px){
            padding: 30px 0 20px 0;
        }
        &__title{
            font-weight: 400;
            font-size: 20px;
            @media(max-width: 992px){
                font-size: 16px;
            }
        }

        &-empty{
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            width: 100%;
            height: 100%;
            min-height: 300px;
            font-size: 18px;

            &:after{
                content: "";
                width: 76%;
                background: hsla(0,0%,89.8%,.5);
                height: 1px;
                position: absolute;
                left: 12%;
                bottom: 0;
            }
        }
    }
}