* {
    font: 400 14px/17px 'Montserrat', sans-serif;
    margin: 0;
    padding: 0;
}

*,
*:after,
*:before {
    box-sizing: border-box;
}

.clearfix:after,
.clearfix:before {
    content: " ";
    display: table;
}

.clearfix:after {
    clear: both;
}

html {
    height: 100%;
}

body{
    display: flex;
	flex-direction: column;
    min-height: 100%;
}

header {
    flex: 0 1 auto;
}
main{
    flex:  1 1 auto; 
    height: 100%;
}
footer {
    flex: 0 1 auto;
}

.container {
    max-width: 1300px;
    position: relative;
}
.ptb{
    padding: 80px 0;

    @media(max-width: 992px){
        padding: 50px 0;
    }
}
img{
    width: 100%;
}

.bg-shadow{
    display: flex;
    justify-content: center;
    position: fixed;
    left: 0;
    top: 0;
    align-items: center;
    background:$black;
    opacity: .2;
    transition: all .3s ease-in-out;
    display: flex;
    width: 100%;
    height: 100%;
    z-index: 2;
}


.control{
    &-block{
        display: flex;
        justify-content: flex-end;
        padding: 20px 0;
        position: relative;

        @media(max-width: 767px){
            padding: 5px 0;
        }

        & button{
            opacity: 1;
                transition: all .3s ease-in-out;
                visibility: visible;

            @media(max-width: 767px){
                font-size: 12px;
                padding: 10px 0;
            }

            &.hide{
                opacity: 0;
                transition: all .3s ease-in-out;
                visibility: hidden;
            }
        }
    }
    &-edit{
        position: absolute;
        right: 0;
    }
    &-remove{
        position: relative;
        margin-right: 8px;
        &:after{ 
            content: ' | ';
            position: absolute;
            right: -5px;
            top: 50%;
            transform: translateY(-60%);
        }
    }
}

.bg-lightgrey{
    background: $lightgrey;
}

.block-head{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    @media(min-width: 992px){
        height: 250px;
    }

    @media(min-width: 768px) and (max-width: 991px){
        height: 200px;
    }
    @media(min-width: 575px) and (max-width: 767px){
        height: 170px;
    }
    @media(max-width: 574px){
        height: 140px;
    }

    & h1 {
        font-size: 34px;
        display: flex;
        justify-content: center;
        text-align: center;
        align-items: flex-end;
        flex-wrap: wrap;
        font-weight: 400;

        @media(min-width: 768px) and (max-width: 991px) {
            font-size: 24px;
        }

        @media(min-width: 575px) and (max-width: 767px) {
            font-size: 20px;
        }

        @media(max-width: 574px) {
            font-size: 18px;
        }
    }

    & span{

        &.price_order{
            color:$red;
            margin-top: 35px;
            font-weight: 500;
            font-size: 30px;
    
            @media(min-width: 768px) and (max-width: 991px){
                font-size: 25px;
                margin-top: 25px;
            }
            @media(min-width: 575px) and (max-width: 768px){
                font-size: 19px;
                margin-top: 15px;
            }
    
            @media(max-width: 574px){
                font-size: 16px;
                margin-top: 10px;
                font-weight: 500;
            }
        }

        &.currency{
            color:$red;
            margin-top: 38px;
            font-weight: 500;
            font-size: 22px;
            margin-left: 4px;
    
            @media(min-width: 768px) and (max-width: 991px){
                font-size: 18px;
                margin-top: 28px;
            }
            @media(min-width: 575px) and (max-width: 768px){
                font-size: 15px;
                margin-top: 16px;
            }
    
            @media(max-width: 574px){
                font-size: 12px;
                margin-top: 10px;
                font-weight: 500;
            }
        }
    }

    & p{
        margin-top: 35px;
        font-size: 24px;
        margin-right: 6px;

        @media(min-width: 768px) and (max-width: 991px){
            font-size: 26px;
            margin-top: 25px;
        }
        @media(min-width: 575px) and (max-width: 768px){
            font-size: 16px;
            margin-top: 15px;
        }

        @media(max-width: 574px){
            font-size: 14px;
            margin-top: 10px;
            font-weight: 500;
        }
    }
}

.block{
    &-mb{
        margin-bottom: 150px;

        @media(max-width: 992px){
            margin-bottom: 100px;
        }

        @media(max-width: 575px){
            margin-bottom: 70px;
        }
    }
}
