
.pagination {

    &-block{
        padding: 60px 0;
    }

    & ul{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        
    }

    & a{
        width: 100%;
        height: 100%;
        font-weight: 500;
        font-size: 14px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
        

    

    &-item {
        list-style: none;
        border-radius: 50%;
        color: $black;
        width:30px;
        height:30px;
        text-decoration: none;
        transition: background-color 0.3s;
        margin-right: 5px;

        &.active {
            background-color: #F3F3F3;
            color: $lightgreen;
        }

        &:first-child{
            display: flex;
            background-image: url(../img/icon/next.svg);
            background-position: center;
            background-repeat: no-repeat;
            background-size:6px;
            transform-origin: center;
            transform: scale(-1);
            transition: all .3s ease-in-out;
            margin-right: 35px;
            margin-left: 5px;
        }
        
        &:last-child{
            display: flex;
            background-image: url(../img/icon/next.svg);
            background-position: center;
            background-repeat: no-repeat;
            background-size:6px;
            transform-origin: center;
            transition: all .3s ease-in-out;
            margin-right: 0;
            margin-left: 35px;
        }
    }
}
