.article {
    padding-top: 40px;
    margin-bottom: 80px;

    @media(min-width: 575px) and (max-width: 767px) {
        padding-top: 30px;
    }

    @media(max-width: 574px) {
        padding-top: 0;
    }

    & P {
        font-size: 16px;
        line-height: 22px;

        @media(max-width: 1300px) {
            font-size: 14px;
            line-height: 19px;
        }
    }

    & .content {

        &-left {
            margin-bottom: 120px;
            position: relative;

            @media(min-width: 768px) and (max-width: 991px) {
                margin-bottom: 100px;
            }

            @media(min-width: 574px) and (max-width: 767px) {
                margin-bottom: 80px;
            }

            @media(max-width: 574px) {
                margin-bottom: 40px;
            }

            & [class^="col-"] {
                padding: 0;
                margin: 0;
            }

            &__img {
                max-width: 600px;

                @media(min-width: 575px) and (max-width: 767px) {
                    max-width: 500px;
                }

                &-block {
                    position: relative;
                }
            }

            &__quote {
                background: #e5e5e5;
                padding: 50px;
                left: calc(50% -100px);
                top: 90px;
                margin-right: 30px;
                position: absolute;

                @media(max-width: 574px) {
                    padding: 35px 60px 35px 25px;
                    margin-left: 0;
                    margin-top: -5px;
                    position: initial;
                    margin-right: 5px;
                }

                @media(min-width: 575px) and (max-width: 767px) {
                    padding: 40px 50px;
                }

                &::after {
                    content: '';
                    z-index: -1;
                    width: 80px;
                    height: 80px;
                    background: $black;
                    position: absolute;
                    display: flex;
                    bottom: -7px;
                    right: -7px;

                    @media(min-width: 768px) and (max-width: 991px) {
                        right: -5px;
                        bottom: -5px;
                        width: 70px;
                        height: 70px;
                    }

                    @media(min-width: 575px) and (max-width: 767px) {
                        right: -5px;
                        bottom: -5px;
                        width: 60px;
                        height: 60px;
                    }
                    @media(max-width: 574px) {
                        right: 1px;
                        bottom: -4px;
                        width: 60px;
                        height: 55px;
                    }
                }

                & > * {
                    font-size: 22px;
                    line-height: 36px;
                    font-weight: 500;

                    @media(max-width: 767px) {
                        font-size: 18px;
                        line-height: 23px;
                    }

                }
            }
        }

        &-right {

            & .content-block {
                width: 50%;
                padding-right: 40px;
                display: flex;
                flex-direction: column;
                align-content: flex-end;

                @media(max-width: 767px) {
                    order: 2;
                    width: 80%;
                }

                @media(max-width: 450px) {
                    width: 100%;
                    padding-right: 0;
                }

                & p {
                    padding-left: 45%;

                    @media(min-width: 768px) and (max-width: 991px) {
                        padding-left: 70px;
                    }

                    @media(max-width: 767px) {
                        width: 85%;
                        padding-left: 0;
                    }

                }
            }

            & .col-12 {
                display: flex;
                @media(max-width: 767px) {
                    flex-direction: column;
                }
            }

            & .title {
                width: 100%;
                margin-top: 120px;
                font-size: 28px;
                font-weight: 500;
                line-height: 47px;
                position: relative;
                display: flex;
                justify-content: flex-end;
                padding-right: 30px;
                padding-bottom: 12px;
                margin-bottom: 50px;

                @media(min-width: 768px) and (max-width: 991px) {
                    font-size: 26px;
                    line-height: 34px;
                    margin-top: 90px;
                }

                @media(max-width: 767px) {
                    justify-content: flex-end;
                    font-size: 24px;
                    line-height: 31px;
                    margin-top: 40px;
                    justify-content: flex-start;
                }

                @media(min-width: 575px) and (max-width: 767px) {
                    font-size: 21px;
                    padding-bottom: 8px;
                }

                @media(max-width: 574px) {
                    font-size: 20px;
                    margin: 55px 0 30px;
                    padding-bottom: 8px;
                }

                &:before {
                    content: '';
                    width: 100%;
                    position: absolute;
                    height: 2px;
                    bottom: 0;
                    left: 0;
                    background: $black;
                }
            }

            &__img {
                
                @media(min-width: 575px) and (max-width: 767px) {
                    max-width: 600px;
                    padding-left: 30px;
                    padding-right: 30px;
                }

                @media(max-width: 574px) {
                    max-width: 450px;
                    padding-left: 20px;
                    padding-right: 20px;
                }

                &-block {
                    display: flex;
                    justify-content: flex-end;
                    width: 50%;
                    margin-right: 5px;

                    @media(min-width: 768px) and (max-width: 992px) {
                        width: 70%;
                    }

                    @media(min-width: 575px) and (max-width: 767px) {
                        justify-content: flex-end;
                        order: 1;
                        width: 100%;
                    }

                    @media(max-width: 675px) {
                        justify-content: center;
                        order: 1;
                        width: 100%;
                    }
                    @media(max-width: 574px) {
                        justify-content: flex-end;
                    }
                }

                &-wrap {
                    position: relative;
                }

                &.spirit {
                    position: absolute;
                    left: 30px;
                    top: -30px;
                    z-index: -1;

                    @media(min-width: 1300px) {
                        left: 60px;
                        top: -60px;
                    }

                    @media(min-width: 574px) {
                        left: 20px;
                        top: -20px;
                    }
                }
            }
        }

        &-center {
            display: flex;
            justify-content: space-between;
            flex-direction: row;
            padding-left: 60px;
            margin-bottom: 150px;

            @media(max-width: 767px) {
                padding-left: 0;
                margin-bottom: 100px;
            }
            @media(max-width: 574px){
                margin-bottom: 80px;
            }

            @media(max-width: 450px) {
                flex-direction: column;
            }

            & .content {
                @media(max-width:767px) {
                    order: 2;
                    width: 55%;
                }

                @media(max-width: 450px) {
                    width: 100%;
                }
            }

            & .title {
                display: inline-flex;
                padding: 10px 30px;
                background: rgba(229, 229, 229, 0.5);
                font-size: 16px;
                line-height: 22px;
                margin-bottom: 40px;
                margin-left: -25px;

                @media(max-width: 767px) {
                    display: none;
                }
            }

            & .years {
                width: 100%;
                max-width: 40%;
                display: flex;
                justify-content: center;
                align-items: center;
                padding: 10px;
                font-size: 32px;
                font-weight: 500;
                line-height: 46px;
                order: 1;

                &:after,
                &:before {
                    content: '-';
                    font-size: inherit;
                    font-weight: inherit;
                    color: inherit;
                }
                @media(min-width: 575px) and (max-width: 788px) {
                    font-size: 28px;
                }

                @media(max-width: 574px) {
                    font-size: 26px;
                    max-width: 45%;
                }

                @media(max-width: 450px) {
                    max-width: 100%;
                }
            }
        }

    }

}
