.basket {
    &-block {
        &-head {
            display: flex;
            justify-content: center;
            align-self: center;
            flex-direction: column;

            @media(min-width: 992px) {
                padding: 80px 0;
            }
            @media(min-width: 768px) and (max-width: 991px) {
                padding: 60px 0;
            }
            @media(max-width: 767px) {
                padding: 50px 0;
            }

            &-wrap {
                background: $lightgrey;
                border-bottom: 1px solid $lightgrey;
            }

            & h2 {
                font-size: 36px;
                letter-spacing: 1px;
                display: flex;
                justify-content: center;
                text-align: center;
                align-items: flex-end;
                flex-wrap: wrap;

                @media(min-width: 768px) and (max-width: 991px) {
                    font-size: 24px;
                }

                @media(min-width: 575px) and (max-width: 767px) {
                    font-size: 20px;
                }

                @media(max-width: 574px) {
                    font-size: 18px;
                }

            }

            & h3 {
                font-size: 20px;
                @media(min-width: 768px) and (max-width: 1200px) {
                    font-size: 16px;
                }
                @media(max-width: 767px) {
                    font-size: 14px;
                }
            }

            & * {
                display: flex;
                justify-content: center;
                align-self: center;

            }
        }

        &-body {
            padding: 70px 0;

            @media(max-width: 991px) {
                padding: 30px 0;
            }

            @media(max-width: 767px) {
                padding: 50px 0;
            }

            @media(max-width: 574px) {
                padding: 40px 0;
            }
        }

        &-footer {
            display: flex;
            flex-direction: column;
            align-items: center;
            position: relative;

            &:before {
                content: '';
                position: absolute;
                right: 0;
                top: 0;
                display: flex;
                width: calc(100% - 176px);
                height: 1px;
                background: #E5E5E5;
                @media(max-width: 992px) {
                    width: 100%;
                }
            }

            &__wrapper {
                width: 100%;
                padding: 50px 0;
                display: flex;
                flex-direction: column;
                align-items: flex-end;

                @media(max-width: 992px) {
                    padding-left: 0;
                }

                & button {
                    width: 100%;
                    max-width: 400px;
                    padding: 13px 20px;
                    
                    @media(min-width: 768px) and (max-width: 991px) {
                        padding: 10px 20px;
                        max-width: 350px;
                    }
                    @media (max-width: 767px) {
                        padding: 10px 20px;
                    }

                    @media(max-width: 575px){
                        max-width: 100%;
                    }
                }
            }
        }
    }
    &-list {
        margin: 0 0 30px;
        padding: 0;
        list-style: none;
        height: 100%;
        position: relative;

        &__item {
            list-style: none;
            display: flex;
            padding: 5px 0;
            background: $white;
      
            
            &.animation{
                animation-name: backOutRight;
                animation-duration: 1s;
                animation-delay: .1s;
            }
            @media(max-width: 767px) {
                padding: 20px 0;
                flex-direction: column;
                align-items: center;
            }
        }
    }

    &-product {
        display: flex;

        &__img {

            &-block {
                display: flex;
                justify-content: center;
                align-items: center;
                margin-right: 30px;
                background: #fff;

                @media(max-width: 768px) {
                    margin-right: 0;
                    margin-bottom: 50px;
                }

                @media(max-width: 400px) {
                    margin-bottom: 36px;
                }
            }

            &-wrapper {
                display: flex;
                width: 100%;
                height: 100%;
                max-width: 170px;
                max-height: 170px;
                display: flex;
                justify-content: center;
                align-items: center;

                @media(max-width: 767px) {
                    max-width: 200px;
                    max-height: 200px;
                }
                @media(max-width: 400px) {
                    max-width: 120px;
                    max-height: 120px;
                }
            }

            max-width: 100%;
            width: auto;
            height: auto;
            max-height: 100%;
            display: flex;
        }

        &__info {
            display: flex;
            justify-content: space-between;
            align-self: center;
            width: 100%;

            @media(max-width: 767px) {
                flex-direction: column;

            }
        }

        &__title {
            width: 100%;
            transition: all 0.3s ease-in-out;
            margin-right: 40px;
            font-size: 22px;
            line-height: 28px;
            display: flex;
            flex-direction: column;
            justify-content: center;

            &-wrap {
                display: flex;
                flex-direction: column;
                margin-right: 15px;

                & p {
                    &:first-child {
                        @media(max-width: 574px) {
                            display: none;
                        }

                    }
                }

            }

            @media(min-width: 575px) and (max-width: 991px) {
                font-size: 18px;
            }

            @media(max-width: 574px) {
                font-size: 20px;
            }

            @media(max-width: 767px) {
                max-width: 100%;
                margin-right: 0;
                margin-bottom: 10px;
                align-items: center;
                justify-content: space-between;
                flex-direction: row;
            }

            &:hover {
                transition: all 0.3s ease-in-out;
                color: $lightgreen;
            }

            & p {
                font-size: 16px;
                @media(max-width: 991px) {
                    font-size: 14px;
                }
            }
        }

        & .select {
            width: 115px;
            height: 50px;

            @media(max-width: 1200px) {
                height: 40px;
                width: 100px;
            }

            &-item {
                & div {
                    padding: 5px 16px;
                }
            }

            &-selected {
                height: 100%;
                background: $lightgrey;

                &:after {
                    top: 22px;

                    @media(max-width: 1200px) {
                        top: 19px;
                    }
                }

                &.select-arrow-active {
                    &:after {
                        top: 14px;
                        @media(max-width: 1200px) {
                            top: 11px;
                        }
                    }
                }
            }
        }

        &__btn {
            display: flex;
            justify-content: flex-end;
            align-items: flex-end;
            flex-direction: column;

            @media(max-width: 767px) {
                width: 50%;
            }

            &-block {
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: space-between;

                @media(max-width: 767px) {
                    max-width: 100%;
                }
            }
        }
        &-price {
            display: flex;
            align-items: flex-end;
            justify-content: flex-end;

            &__value {
                font-size: 26px;
                @media (max-width: 991px) {
                    font-size: 22px;
                }
            }
            &__currency {
                font-size: 16px;
                margin-left: 3px;
                line-height: 11px;
            }

            &-old{
                position: relative;
                height: 100%;
                display: flex;
                align-items: flex-end;
                margin-right: 40px;
        
                &:after{
                    content: '';
                    width: 100%;
                    height: 1px;
                    background: $red;
                    position: absolute;
                    top: 50%;
                    left: 0;
                }
        
                &__value{   
                    font-size: 18px;
                    font-weight: 400;
                    color:$red;
                }
        
                &__currency{
                    margin-left: 4px;
                    font-size: 16px;
                    font-weight: 400;
                    color:$red;
                }
            }
        }

        &__remove {
            display: flex;
            justify-content: flex-end;
            font-size: 14px;
            color: $red;
            padding: 10px 0;
            @media(max-width: 991px) {
                font-size: 12px;
            }
            @media(max-width: 767px) {
                padding: 10px 0;
                text-transform: uppercase;
            }
        }
    }

    &-middle {
        &__delivery {
            display: flex;
            justify-content: space-between;
            border-bottom: 1px solid $lightgrey;
            padding: 15px 0;
            width: 100%;
            @media(min-width: 575px) and (max-width: 1200px) {
                padding: 10px 0;
            }

            &-title {
                }

            &-value {

                display: flex;
                justify-content: flex-end;
                font-size: 18px;
                @media(min-width: 575px) and (max-width: 991px) {
                    font-size: 14px;
                }
                @media(max-width: 574px) {
                    font-size: 16px;
                }
            }
        }
    }

    &-total {
        &__price {
            display: flex;
            justify-content: space-between;
            width: calc(100% - 176px);
            margin-bottom: 40px;

            @media(max-width: 992px) {
                width: 100%;
            }

            &-currency {
                display: flex;
                font-size: 16px;
                width: auto;
                margin-left: 5px;
            }

            &-title {
                display: flex;
                font-size: 18px;
                line-height: 20px;
                margin: 0;

                @media(max-width: 767px) {
                    font-size: 16px;
                }
            }

            &-value {
                display: flex;

                align-items: flex-end;
                @extend .basket-product-price__value;
            }
        }
    }

    &-empty {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        position: relative;

        &:after {
            content: '';
            width: 76%;
            background: $lightgrey;
            height: 1px;
            position: absolute;
            left: 12%;
            bottom: 0;
        }
        & a {
            display: flex;
            justify-content: center;
            align-items: center;
            text-transform: uppercase;
            background: $lightgrey;
            color: $black;
            max-width: 380px;
            width: 100%;
            height: 45px;
            margin-bottom: 60px;
        }

        & span {
            font-size: 17px;
            margin-bottom: 60px;
            font-weight: 400;
            line-height: 21px;
            margin-bottom: 60px;
        }
    }

 

    &-promo-block{
        display: flex;
        justify-content: space-between;
        align-items:center;
        width: 100%;
        max-width:400px;
        margin-bottom: 17px;
            @media (max-width: 575px){
                max-width: 100%
            }

            @media (min-width: 768px) and (max-width: 991px){
                max-width: 350px
            }

        & p{
            word-wrap: break-word;
            text-transform: uppercase;

            &::before{
                @media(min-width: 992px){
                    content: 'Введите '
                }
            }
        }

        & input{
            max-width:190px;
            min-height: 22px;
            max-height: 22px;
            border-color: $black;
        }
        
        & div{
            display: flex;
        }

        .discount{
            padding: 10px;
            background: #00B912;
            color: white;
            text-transform: uppercase;
            font-weight: 500;
            height: 40px;
            display: flex;
            align-items:center;
            

            &-err{
                padding: 10px;
                height: 40px;
                position: relative;
                font-weight: 500;
                color:$red;
                text-transform: uppercase;
                display: flex;
                align-items:center;

                &:after{
                    content:'';
                    height: 2px;
                    width: 100%;
                    position: absolute;
                    left: 0;
                    bottom: 6px;
                    background: $red;
                }
            }
        }
        .remove-discount{
            width: 30px!important;
            height: 40px !important;
            position: relative;

            & svg{
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translateY(-50%);
            }
        }
    }
}
