.breadcrumb {
    padding: 5px 0;
    list-style: none;
    margin: 0;

  &-block{
    border-bottom:1px solid $lightgrey;
    border-top:1px solid $lightgrey;

    @media(max-width: 991px){
      margin-top: 0;
    border-top:none              ;

    }
  }

   & li {
    display: inline;
    font-size: 12px;
    line-height: 15px;
    font-weight: 500;

    @media(max-width: 991px){
      font-size: 10px;
    }
    
    & + li{
      &:before {
        padding: 8px;
        color: $black;
        content: "\2022";
      }
    }
    
    & a {
      color: $black;
      text-decoration: none;
      font-size: 12px;

      @media(max-width: 991px){
        font-size: 10px;
      }

      &:hover {
        color: $black;
        text-decoration: underline;
      }
    }
  }
}